import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import Checkbox from 'components/Checkbox'

export default ({ state, setState }) => {
  return <Fragment>
    <input type="text" placeholder="a place to Tab from" />

    <StyleguideExample
      title="default"
      render={() =>
        <Checkbox
          value={state.example1}
          onChange={example1 => { setState({example1}) }}
        />
      }
    />

    <StyleguideExample
      title="with a label"
      render={() =>
        <Checkbox
          value={state.example2}
          onChange={example2 => { setState({example2}) }}
          label="I am over 14"
        />
      }
    />

    <StyleguideExample
      title="with a label and disabled"
      render={() =>
        <Checkbox
          value={state.example3}
          onChange={example3 => { setState({example3}) }}
          label="I like purple drink"
          disabled
        />
      }
    />

    <StyleguideExample
      title="with a label and disabled and checked"
      render={() =>
        <Checkbox
          checked
          label="I like weird al"
          disabled
        />
      }
    />

    <StyleguideExample
      title="with a label and reverse"
      render={() =>
        <Checkbox
          reverse
          value={state.example5}
          onChange={example5 => { setState({example5}) }}
          label="I like to reverse it"
        />
      }
    />

    <input type="text" placeholder="a place to Tab to" />
  </Fragment>
}
