import { h } from 'preact'
import { useState } from 'preact/hooks'

import StyleguideExample from 'components/StyleguideExample'
import ElevateButton from 'components/ElevateButton'

export default () => {
  const [elevated, setState] = useState(false)
  return <div>
    <StyleguideExample
      title="default"
      render={() =>
        <ElevateButton {...{
          elevated,
          count: 13 + (elevated ? 1 : 0),
          onClick: () => { setState(!elevated) },
        }}/>
      }
    />
    <StyleguideExample
      title="disabled"
      render={() =>
        <ElevateButton disabled count={11} />
      }
    />
  </div>
}
