import { h } from 'preact'
import { useState } from 'preact/hooks'
import useToggle from 'lib/useToggleHook'

import InterfaceHelp, { useInterfaceHelpVisibility } from 'components/InterfaceHelp'
import Button from 'components/Button'
import IconButton from 'components/IconButton'
import Switch from 'components/Switch'

const positions = `tl tr bl br`.split(' ')

export default () => {
  const [visible, show, hide] = useInterfaceHelpVisibility()
  const [targetOn, showTarget, hideTarget] = useToggle(true)
  const [offset, setOffset] = useState(0)

  return <div>
    <p>
      <label>
        <Switch {...{
          checked: visible,
          onChange: visible ? hide : show,
        }}/>
        <span>help mode {visible ? 'on' : 'off'}</span>
      </label>
    </p>

    <hr/>

    <InterfaceHelp content="this button is so dumb">
      <Button type="primary">confusing button</Button>
    </InterfaceHelp>

    <br/>

    <InterfaceHelp content="this has multiple children">
      you should <Button type="primary">push me</Button>
      <span> if you want</span>
    </InterfaceHelp>

    <br/>

    <InterfaceHelp content="you should not see this"/>
    <InterfaceHelp content="you should not see this">
      <Empty/>
    </InterfaceHelp>

    <p>
      <label>
        <Switch {...{
          checked: targetOn,
          onChange: targetOn ? hideTarget : showTarget,
        }}/>
        <span>moving target {targetOn ? 'present' : 'missing'}</span>
      </label>
    </p>

    <InterfaceHelp content="this help icon should come and go as the target does">
      {targetOn && <Button type="primary">moving target</Button>}
    </InterfaceHelp>

    <hr/>

    <p>
      <span>Occaecat deserunt tempor aliqua elit </span>
      <InterfaceHelp content="this button is so dumb">
        <IconButton type="home" />
      </InterfaceHelp>
      <span> ea eu magna enim irure. Aute fugiat ad velit non qui sit aliquip.
      Est irure adipisicing voluptate consectetur commodo laboris nulla veniam
      officia. Dolore quis quis </span>
      <InterfaceHelp content="with some more info">confusing text</InterfaceHelp>
      <span> dolor incididunt esse officia adipisicing. Quis pariatur ullamco velit
      est dolore labore deserunt aute excepteur in eiusmod qui aliquip in. </span>
      <InterfaceHelp p="inline" content="this icon should be inline">inline icon</InterfaceHelp>
      <span> Exercitation irure cupidatat voluptate ullamco non aliqua duis. Cupidatat
      ullamco anim veniam ea et ut proident labore occaecat ut fugiat aute. </span>
      <InterfaceHelp content={''}>no help here</InterfaceHelp>
      <span> consectetur commodo laboris nulla veniam officia. Dolore quis quis</span>
    </p>

    <InterfaceHelp content={''}>
      <Button type="primary">this button should not have help</Button>
    </InterfaceHelp>

    <hr/>

    <div>
      <input
        type="range"
        min={-4}
        max={4}
        value={offset}
        onInput={e => { setOffset(e.target.value) }}
      />
    </div>
    <div
      className="InterfaceHelp-examples-positions"
      style={{
        backgroundColor: 'black',
        padding: `20px`,
      }}
    >
      {positions.map(position =>
        <p>
          <InterfaceHelp
            p={position}
            offset={offset}
            title="Position Tests"
            content={`position="${position}" offset="${offset}"`}
          >
            <Button type="normal">
              <div style={{ width: '200px' }}>position="{position}" offset="{offset}"</div>
            </Button>
          </InterfaceHelp>
        </p>
      )}
    </div>

  </div>
}

const Empty = () => {}
