import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import Link from 'components/Link'
import BreadCrumbs from 'components/BreadCrumbs'

export default ({ location }) => {
  let href = ''
  const crumbs = []
  location.pathname.split('/').forEach(part => {
    if (part === '' || part === '/') return
    href += '/' + part
    crumbs.push({ key: href, href, value: part })
  })
  return <div>
    {
      [
        '',
        '/settings',
        '/settings/notifications',
        '/settings/notifications/email',
        '/settings/profiles',
        '/settings/profiles/avatar',
        '/a/b/c',
        '/a/b/c/d',
        '/a/b/c/d/e',
        '/a/b/c/d/e/f',
        '/a/b/c/d/e/f/g',
      ].map(pathname => {
        const href = '/components/BreadCrumbs' + pathname
        return <div><Link type="link" href={href}>{href}</Link></div>
      })
    }
    <StyleguideExample
      title="default"
      render={() =>
        <BreadCrumbs {...{crumbs}}/>
      }
    />
    <StyleguideExample
      title="disabled"
      render={() =>
        <BreadCrumbs {...{crumbs}} disabled />
      }
    />
  </div>
}
