import { h } from 'preact'
import { useCallback } from 'preact/hooks'
import PropTypes from 'prop-types'

import { fileToImage, resizeImage } from 'lib/imageHelpers'
import Icon from 'components/Icon'
import Overlay from 'components/Overlay'
import './index.sass'

export default function ResizeImageInput({
  className = '',
  title,
  width,
  height,
  circle,
  disabled,
  unsaved,
  onChange,
  onError,
  value,
  defaultValue,
  resizeTo,
  ...props
}){
  const onFileSelect = useCallback(
    event => {
      const file = event.target.files[0]
      fileToImage(file)
        .then(image => resizeImage({ image, width, height, resizeTo }))
        .then(onChange)
        .catch(error => { if (onError) onError(error) })
    },
    [width, height, resizeTo, onError, onChange]
  )

  const _value = value || defaultValue
  const empty = !value

  className = `ResizeImageInput ${className}`
  if (circle) className += ' ResizeImageInput-circle'
  if (disabled) className += ' ResizeImageInput-disabled'
  if (unsaved) className += ' ResizeImageInput-unsaved'

  return <div {...props} className={className}>
    <div
      className="ResizeImageInput-imageWrapper"
      style={{width, height}}
    >
      { _value && <img src={_value} /> }
      <Overlay hidden={!empty}><Icon type="edit" /></Overlay>
      <input
        type="file"
        accept="image/*"
        onChange={onFileSelect}
        title={title}
        disabled={disabled}
      />
    </div>
  </div>
}

ResizeImageInput.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string,
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  circle: PropTypes.bool.isRequired,
  value: PropTypes.string,
  defaultValue: PropTypes.string,
  disabled: PropTypes.bool,
  unsaved: PropTypes.bool,
  backgroundImageUrl: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  onError: PropTypes.func.isRequired,
  resizeTo: PropTypes.oneOf(['fill', 'fit']),
}

ResizeImageInput.defaultProps = {
  resizeTo: 'fit',
  circle: false,
  unsaved: false,
  title: 'Click here to upload a new image',
}
