import { h } from 'preact'
import { useState } from 'preact/hooks'

import useToggle from 'lib/useToggleHook'

import Button from 'components/Button'
import StyleishModal from 'components/StyleishModal'
import WYSIWYGEditor from 'components/WYSIWYGEditor'

export default () => {
  const [isModalOpen, openModal, closeModal] = useToggle(false)
  const [value, onChange] = useState(initialValue)

  const editorExample = <div style={{border: '1px solid purple'}}>
    <WYSIWYGEditor {...{ value, onChange }}/>
  </div>
  return <div>

    {editorExample}
    <hr/>
    <Button type="primary" onClick={openModal}>open modal</Button>
    <StyleishModal
      title="Example of a <StyleishModal />"
      open={isModalOpen}
      onClose={closeModal}
    >
      {editorExample}
    </StyleishModal>
  </div>
}

const initialValue = `
<p>
this is a link to <a href="http://example.com">example.com</a>.
</p>
`
