import { h, Fragment } from 'preact'
import { useState } from 'preact/hooks'

import OrganizationListMember from 'components/OrganizationListMember'
import PeopleListMember from 'components/PeopleListMember'
import Button from 'components/Button'
import StyleguideExample from 'components/StyleguideExample'
import IconRowAccordion from 'components/IconRowAccordion'

import jared from 'components/PeopleListMember/examples/jared'
import sylvan from 'components/PeopleListMember/examples/sylvan'
import oshoOrganization from 'components/OrganizationListMember/examples/osho'
import jlincOrganization from 'components/OrganizationListMember/examples/jlinclabs'

export default ({ location }) => {
  const [selected, setSelected] = useState({})

  const buttons = <Fragment>
    <Button type="primary" value="Join" />
    <Button type="primary" value="SISA" />
  </Fragment>

  return <div>
    <StyleguideExample
      title="controlled with useState"
      render={() =>
        <IconRowAccordion {...{
          expandedIndex: selected,
          onChange: setSelected,
        }}>
          <div>
            <OrganizationListMember organization={oshoOrganization} {...{buttons}}/>
            <OrganizationPreview organization={oshoOrganization}/>
          </div>
          <div>
            <OrganizationListMember organization={jlincOrganization} {...{buttons}}/>
            <OrganizationPreview organization={jlincOrganization}/>
          </div>
          <div>
            <PeopleListMember publicProfile={jared} {...{buttons}} linkToProfile={false}/>
            <PersonPreview person={jared}/>
          </div>
          <div>
            <PeopleListMember publicProfile={sylvan} {...{buttons}} linkToProfile={false}/>
            <PersonPreview person={sylvan}/>
          </div>
        </IconRowAccordion>
      }
    />
    <StyleguideExample
      title="controlled by url"
      render={() =>
        <IconRowAccordion {...{
          expandedIndex: (
            'osho jlinc jared sylvan'.split(' ').indexOf(
              location.pathname.match(/^\/components\/IconRowAccordion\/(\w+)/) && RegExp.$1
            )
          )
        }}>
          <div>
            <OrganizationListMember
              href="/components/IconRowAccordion/osho"
              organization={oshoOrganization}
              {...{buttons}}
            />
            <OrganizationPreview organization={oshoOrganization}/>
          </div>
          <div>
            <OrganizationListMember
              href="/components/IconRowAccordion/jlinc"
              organization={jlincOrganization}
              {...{buttons}}
            />
            <OrganizationPreview organization={jlincOrganization}/>
          </div>
          <div>
            <PeopleListMember
              href="/components/IconRowAccordion/jared"
              publicProfile={jared}
              {...{buttons}}
            />
            <PersonPreview person={jared}/>
          </div>
          <div>
            <PeopleListMember
              href="/components/IconRowAccordion/sylvan"
              publicProfile={sylvan}
              {...{buttons}}
            />
            <PersonPreview person={sylvan}/>
          </div>
        </IconRowAccordion>
      }
    />
  </div>
}


function OrganizationPreview({ organization }){
  return <div>
    <p>{organization.name}</p>
    <p>{organization.tag_line}</p>
  </div>
}

function PersonPreview({ person }){
  return <div>
    <p>{person.displayName}</p>
    <p>{person.bio}</p>
  </div>
}
