import { h } from 'preact'
import Loading from 'components/Loading'

export default () =>
  <div>
    {
      examples().map(props =>
        <div>
          <div>{JSON.stringify(props)}</div>
          <Loading {...props}/>
        </div>
      )
    }
  </div>


function examples(){
  const set = []
  ;['fullScreen', 'fullPage', 'block'].forEach(type => {
    ['sm', 'md', 'lg'].forEach(size => {
      set.push({type, size})
    })
  })
  return set
}
