import { h } from 'preact'
import { useState, useEffect } from 'preact/hooks'

import {
  loadImage,
  deanimateImage,
  fileToImageDataURL,
  ICON_SIZE,
  AVATAR_SIZE,
  BANNER_SIZE,
} from 'lib/imageHelpers'

import CropImage from 'components/CropImage'
import Header from 'components/Header'
import EndUserAvatar from 'components/EndUserAvatar'
import OrganizationBanner from 'components/OrganizationBanner'
import OrganizationIcon from 'components/OrganizationIcon'

import exampleIconSrc from 'components/CropImageInput/examples/icon.png'
import exampleAvatarSrc from 'components/CropImageInput/examples/avatar.png'
import exampleBannerSrc from 'components/CropImageInput/examples/banner.png'
import hawaiiSrc from 'components/CropImageInput/examples/hawaii.png'

const imageUrls = {
  exampleIcon: exampleIconSrc,
  exampleAvatar: exampleAvatarSrc,
  exampleBanner: exampleBannerSrc,
  hawaii: hawaiiSrc,
}

export default () => {
  const [images, setImages] = useState()
  useEffect(
    () => {
      const images = {}
      Promise.all(
        Object.entries(imageUrls).map(async ([name, url]) => {
          images[name] = await loadImage(url)
        })
      ).then(() => { setImages(images) })
    },
    []
  )
  return images ? <Examples {...{images}}/> : <div>loading</div>
}


function fileInputEventToSrc(event){
  return fileToImageDataURL(event.target.files[0])
    .then(loadImage)
    .then(deanimateImage)
    .then(image => image)
}

function Examples({ images }){
  const [example1SrcImage, setExample1SrcImage] = useState(images.exampleBanner)
  const [example1Image, setExample1Image] = useState()
  const [example2SrcImage, setExample2SrcImage] = useState(images.exampleIcon)
  const [example2Image, setExample2Image] = useState()
  const [example3SrcImage, setExample3SrcImage] = useState(images.exampleAvatar)
  const [example3Image, setExample3Image] = useState()

  return <div>
    <Header size="md">BANNER SIZE</Header>
    <div>
      <input
        type="file"
        onChange={event => {
          fileInputEventToSrc(event).then(setExample1SrcImage)
        }}
      />
    </div>
    <div style={{ width: BANNER_SIZE.width, maxWidth: '100%' }}>
      <CropImage {...{
        key: example1SrcImage.src,
        height: BANNER_SIZE.height,
        width: BANNER_SIZE.width,
        image: example1SrcImage,
        onChange: setExample1Image,
        backgroundFillStyle: 'white',
      }}/>
    </div>
    <OrganizationBanner organization={{ consumer_banner: example1Image }}/>

    <hr/>
    <Header size="md">ICON SIZE</Header>
    <div>
      <input
        type="file"
        onChange={event => {
          fileInputEventToSrc(event).then(setExample2SrcImage)
        }}
      />
    </div>
    <div style={{ width: ICON_SIZE.width, maxWidth: '100%' }}>
      <CropImage {...{
        height: ICON_SIZE.height,
        width: ICON_SIZE.width,
        image: example2SrcImage,
        onChange: setExample2Image,
      }}/>
    </div>
    <OrganizationIcon organization={{ consumer_icon: example2Image }} size="lg" />

    <hr/>
    <Header size="md">AVATAR SIZE</Header>
    <div>
      <input
        type="file"
        onChange={event => {
          fileInputEventToSrc(event).then(setExample3SrcImage)
        }}
      />
    </div>
    <div style={{ width: AVATAR_SIZE.width, maxWidth: '100%' }}>
      <CropImage {...{
        circle: true,
        height: AVATAR_SIZE.height,
        width: AVATAR_SIZE.width,
        image: example3SrcImage,
        onChange: setExample3Image,
      }}/>
    </div>
    <EndUserAvatar publicProfile={{avatar: example3Image}} />

  </div>
}
