import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import OrganizationIcon from 'components/OrganizationIcon'
import exampleJLINCLKnotUrl from './example-jlinc-knot.png'

const jlinclabs = {
  apikey: 'jlinclabs',
  name: 'JLINC Labs',
  consumer_icon: exampleJLINCLKnotUrl,
}

export default () => {
  const examples = bordered =>
    <div className="OrganizationIconExamples">
      <style>
        {`
          .OrganizationIconExamples{
            background-color: var(--background-grey);
          }
          .OrganizationIconExamples .OrganizationIcon{
            // outline: 1px dotted grey;
          }
        `}
      </style>
      <div><b>NOTE:</b> Border is only for styleguide examples</div>
      <StyleguideExample
        title={`${bordered ? 'bordered: ' : ''}when src is empty`}
        render={() =>
          <div>
            <div><OrganizationIcon bordered={bordered} size="lg" /></div>
            <div><OrganizationIcon bordered={bordered} size="md" /></div>
            <div><OrganizationIcon bordered={bordered} size="sm" /></div>
          </div>
        }
      />

      <StyleguideExample
        title={`${bordered ? 'bordered: ' : ''}when src is not empty`}
        render={() =>
          <div>
            <div><OrganizationIcon bordered={bordered} organization={jlinclabs} size="lg" /></div>
            <div><OrganizationIcon bordered={bordered} organization={jlinclabs} size="md" /></div>
            <div><OrganizationIcon bordered={bordered} organization={jlinclabs} size="sm" /></div>
          </div>
        }
      />

      <StyleguideExample
        title={`${bordered ? 'bordered: ' : ''}with organization but no icon`}
        render={() =>
          <div>
            <div><OrganizationIcon bordered={bordered} organization={{name: 'MM'}} size="lg" /></div>
            <div><OrganizationIcon bordered={bordered} organization={{name: 'MM'}} size="md" /></div>
            <div><OrganizationIcon bordered={bordered} organization={{name: 'MM'}} size="sm" /></div>
          </div>
        }
      />
      <StyleguideExample
        title={`${bordered ? 'bordered: ' : ''}organization with weird names`}
        render={() =>
          <div>
            <OrganizationIcon bordered={bordered} organization={{name: 'Money Market'}} size="sm" />
            <OrganizationIcon bordered={bordered} organization={{name: '37 Signals'}} size="sm" />
            <OrganizationIcon bordered={bordered} organization={{}} size="sm" />
          </div>
        }
      />
    </div>

  return <div>
    {examples(false)}
    {examples(true)}
  </div>
}
