import { h } from 'preact'
import Button from 'components/Button'

export default () => {
  const buttons = ({disabled}) =>
    Button.TYPES.map(type =>
      <p>
        <div>
          {`<Button type="${type}" ${disabled ? 'disabled' : ''}> Click Me </Button>`}
        </div>
        <div>
          <Button disabled={disabled} type={type}> Click Me </Button>
        </div>
      </p>
    )
  return <div>
    {buttons({})}
    {buttons({disabled: true})}
  </div>
}
