import { h, Fragment } from 'preact'
import PropTypes from 'prop-types'

import classNames from 'lib/classNames'

import Button from 'components/Button'
import './index.sass'

export default function WizardSideNav({className, disabled, steps}) {
  return <div className={classNames('WizardSideNav', { className, disabled })}>
    {steps.map(step =>
      <WizardSideNavButton {...{
        ...step,
        disabled: disabled || step.disabled,
      }}/>
    )}
  </div>
}

WizardSideNav.propTypes = {
  className: PropTypes.string,
  disabled: PropTypes.bool,
  steps: PropTypes.arrayOf(
    PropTypes.shape({
      ...Button.propTypes,
      title: PropTypes.string.isRequired,
      selected: PropTypes.bool.isRequired,
      disabled: PropTypes.bool.isRequired,
      complete: PropTypes.bool.isRequired,
      visited: PropTypes.bool.isRequired,
    })
  ).isRequired,
}

function WizardSideNavButton({
  title,
  selected,
  disabled,
  visited,
  complete,
  ...props
}){
  return <Button {...{
    type: 'none',
    tabIndex: -1,
    ...props,
    className: classNames('WizardSideNav-button', { selected }),
    disabled: disabled || !visited,
  }}>
    <svg height="50" width="20">
      <circle className="WizardSideNav-button-circle" cx="10" cy="19" r="8" />
      {complete &&
        <Fragment>
          <circle className="WizardSideNav-button-complete-circle" cx="10" cy="19" r="4" />
          <line className="WizardSideNav-button-bottomline" x1="10" y1="28" x2="10" y2="50"/>
        </Fragment>
      }
    </svg>
    <span>{title}</span>
  </Button>
}
