import { h, Fragment } from 'preact'
import moment from 'moment'

import Header from 'components/Header'
import TimeAgo from 'components/TimeAgo'

export default () =>
  <div>
    <p>now: <TimeAgo time={new Date()} /></p>

    <Header size="md" underlined>just now</Header>
    {Array(5).fill().map((_, i) =>
      <p key={i}>{i} seconds ago: <TimeAgo time={moment().subtract(i * 1000).toDate()} /></p>
    )}

    <Header size="md" underlined>in the future</Header>
    {Array(5).fill().map((_, i) =>
      <p key={i}>{i} seconds from now: <TimeAgo time={moment().add(i * 1000).toDate()} /></p>
    )}

    <Header size="md" underlined>tiny</Header>
    {['seconds', 'minutes', 'hours', 'days', 'months', 'years'].map(range =>
      <Fragment>
        <p>one {range} ago: <TimeAgo tiny time={moment().subtract(1, range).toDate()}/></p>
        <p>three {range} ago: <TimeAgo tiny time={moment().subtract(3, range).toDate()}/></p>
      </Fragment>
    )}
  </div>
