import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import EndUserAvatar from 'components/EndUserAvatar'
import example1 from './example.jpg'
import example2 from './example2.jpg'

const EXAMPLES = [
  undefined,
  example1,
  example2,
  'https://localhost:1234/not-going-to-load.jpeg',
]

export default () => {
  const examples = props =>
    <div>
      {EXAMPLES.map(example =>
        <div>
          {EndUserAvatar.SIZES.map(size =>
            <EndUserAvatar {...props} publicProfile={{avatar: example}} size={size} />
          )}
        </div>
      )}
    </div>
  return <div>
    <StyleguideExample
      title="default"
      render={() => examples()}
    />
    <StyleguideExample
      title="bordered"
      render={() => examples({bordered: true})}
    />
  </div>
}
