import { h } from 'preact'

import { useSet } from 'lib/useSetHook'

import Header from 'components/Header'
import Button from 'components/Button'
import Alert from 'components/Alert'
import ErrorMessage from 'components/ErrorMessage'
import PageAlerts from 'components/PageAlerts'


export default function ErrorsPage(){
  const [pageAlerts, createPageAlert, dismissPageAlert] = useSet()
  const createExamplePageAlert = ({type, message}) => {
    const alert = { id: alertSequence++, type, message }
    createPageAlert(alert)
    setTimeout(() => { dismissPageAlert(alert) }, 5000)
  }
  return <div>
    <PageAlerts alerts={[...pageAlerts]} onDismiss={dismissPageAlert}/>

    <Header size="xl">Errors</Header>

    <Header size="lg" underlined>Alerts</Header>
    {Object.entries(alertExamples).map(([type, message]) =>
      <div><Alert type={type}>{message}</Alert></div>
    )}

    <Header size="lg" underlined>Dismissable Alerts</Header>
    {Object.entries(alertExamples).map(([type, message]) =>
      <div><Alert type={type} onDismiss={noop}>{message}</Alert></div>
    )}

    <Header size="lg" underlined>Errors</Header>
    <ErrorMessage error={exampleError} dismissable={false}/>
    <ErrorMessage error={exampleError} />

    <Header size="lg" underlined>Page Alerts</Header>
    {Object.entries(alertExamples).map(([type, message]) =>
      <Button
        type="normal"
        onClick={() => { createExamplePageAlert({type, message}) }}
      >Create {type} Page Alert</Button>
    )}
  </div>
}

ErrorsPage.pageTitle = 'ErrorsPage - Styleguide'

let alertSequence = 1
const noop = () => {}
const exampleError = new Error(`just a sample error`)

const alertExamples = {
  success: 'Your Face was saved successfully.',
  info: 'Something boring just happened.',
  warning: 'The radiation shield on your monitor has failed!',
  error: 'Unable to save the planet. Please refresh and try again.',
}
