import { h } from 'preact'
import { useEffect } from 'preact/hooks'

import TagsInput from 'components/TagsInput'
import Form from 'components/Form'
import Button from 'components/Button'
import InspectObject from 'components/InspectObject'

export default ({ state, setState }) => {
  useEffect(
    () => {
      setState({
        noises: ['bark', 'clap'],
        people: ['Brian'],
      })
    },
    [],
  )
  return <div>
    <div>
      Normal TagsInput
    </div>
    <TagsInput
      value={state.colors}
      onChange={colors => setState({ colors })}
      placeholder="enter colors"
    />
    <div>
      Disabled TagsInput
    </div>
    <TagsInput
      value={state.noises}
      onChange={noises => setState({ noises })}
      placeholder="enter noises"
      disabled
    />
    <div>
      Unsaved TagsInput
    </div>
    <TagsInput
      value={state.people}
      onChange={people => setState({ people })}
      placeholder="enter people"
      unsaved
    />
    <Form>
      <div>
        required
      </div>
      <TagsInput
        required
        value={state.favLangs}
        onChange={favLangs => setState({ favLangs })}
        placeholder="perl"
      />
      <Button submit type="primary" value="submit"/>
    </Form>
    <InspectObject object={state} />
  </div>
}
