import { h } from 'preact'
import StyleguideExample from 'components/StyleguideExample'
import SearchInput from 'components/SearchInput'
import InspectObject from 'components/InspectObject'

export default ({ state, setState }) =>
  <div>
    <StyleguideExample
      title="Normal SearchInput"
      render={() =>
        <SearchInput
          value={state.thingsFilter}
          onInput={thingsFilter => setState({ thingsFilter })}
          placeholder="Filter Things"
        />
      }
    />
    <StyleguideExample
      title="with onClear"
      render={() =>
        <SearchInput
          value={state.thingsFilter}
          onInput={thingsFilter => setState({ thingsFilter })}
          placeholder="Filter Things"
          onClear={() => {
            setState({ thingsFilter: 'CLEARED' })
            return false
          }}
        />
      }
    />
    <StyleguideExample
      title="Disabled SearchInput"
      render={() =>
        <SearchInput
          value=""
          placeholder="filter cars"
          disabled
        />
      }
    />
    <StyleguideExample
      title="Unsaved SearchInput"
      render={() =>
        <SearchInput
          value={state.thingsFilter}
          onInput={thingsFilter => setState({ thingsFilter })}
          placeholder="Filter Things"
          unsaved
        />
      }
    />
    <InspectObject object={state} />
  </div>
