import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import TabsMenu from 'components/TabsMenu'

export default ({ location, state, setState }) =>
  <div>
    <StyleguideExample
      title="controlled by parent state"
      render={() =>
        <TabsMenu
          currentTabIndex={state.animalsTabIndex || 0}
          onTabClick={animalsTabIndex => { setState({ animalsTabIndex }) }}
          tabs={[
            {
              title: 'dogs',
              content: 'DOGS!!!',
            },
            {
              title: 'cats',
              content: 'CATS!!!',
            },
          ]}
        />
      }
    />

    <StyleguideExample
      title="controlled by url"
      render={() =>
        <TabsMenu
          currentTabIndex={location.query.t === 'cats' ? 1 : 0}
          tabs={[
            {
              href: `${location.withQuery({t:null})}`,
              title: 'dogs',
              content: 'DOGS!!!',
            },
            {
              href: `${location.withQuery({t:'cats'})}`,
              title: 'cats',
              content: 'CATS!!!',
            },
          ]}
        />
      }
    />

    <StyleguideExample
      title="fullWidth"
      render={() =>
        <TabsMenu
          fullWidth
          currentTabIndex={
            [undefined, 'dashboard', 'settings'].indexOf(location.query.p)
          }
          tabs={[
            {
              href: `${location.withQuery({p:null})}`,
              title: 'Profile',
              content: 'a profile page',
            },
            {
              href: `${location.withQuery({p:'dashboard'})}`,
              title: 'Dashboard',
              content: 'your dashboard page',
            },
            {
              href: `${location.withQuery({p:'settings'})}`,
              title: 'Settings',
              content: 'your settings page',
            },
          ]}
        />
      }
    />
  </div>
