ar(--content-box-box-shadow)\n\n@mixin delayInputFocusBorder()\n  box-shadow: 0 0 0 1px transparent, 0 0 0 3px transparent\n  transition: box-shadow 0ms\n  transition-delay: 10ms\n\n@mixin inputFocusBorder()\n  box-shadow: 0 0 0 1px white, 0 0 0 3px var(--input-focus-border-color)\n\n@mixin undoInputFocusBorder()\n  box-shadow: none\n\n@mixin inputBorder()\n  border: 1px solid var(--border-grey)\n  &:focus, &:focus-within\n    +inputFocusBorder\n\n@mixin undoInputBorder()\n  border: 1px solid transparent\n  &:focus, &:focus-within\n    +undoInputFocusBorder\n\n@mixin fillPositionedParent()\n  position: absolute\n  top: 0\n  left: 0\n  bottom: 0\n  right: 0\n  height: 100%\n  width: 100%\n\n@mixin spaceVertically\n  & + &\n    margin-top: var(--vertical-space)\n\n@mixin noSelectOrDrag\n  -moz-user-select: none\n  -webkit-user-select: none\n  -ms-user-select: none\n  user-select: none\n  -webkit-user-drag: none\n  user-drag: none\n  -webkit-touch-callout: none\n\n@mixin shroud\n  position: fixed\n  top: 0\n  left: 0\n  bottom: 0\n  right: 0\n  width: 100vw\n  background-color: rgba(0,0,0,0.5)\n  z-index: 1000\n\n@mixin isPWA\n  .isPWA &\n    @content\n\n@mixin isDataYogi\n  html.isDataYogi &\n    @content\n\n@mixin circledAlert\n  font-size: 80%\n  line-height: 6px\n  min-width: 20px\n  border-radius: 100px\n  padding: calc(var(--horizontal-padding) / 2)\n  display: flex\n  justify-content: center\n\n@mixin centeredColumn\n  +onScreensWiderThan('tablet')\n    width: 61.803%\n    margin-left: auto\n    margin-right: auto\n\n@mixin faded\n  opacity: 0.5\n  &:hover, &:active, &:focus\n    opacity: 1\n\n@mixin onDevicesWithHover\n  @media not all and (hover: none)\n    @content\n\n@mixin padded\n  padding: var(--vertical-padding) var(--horizontal-padding)\n\n@mixin spaced\n  margin: var(--vertical-space) var(--horizontal-space)\n\n@mixin disabled-button\n  opacity: 0.3\n  cursor: initial\n\n@mixin clearfix\n  display: flow-root\n"],"sourceRoot":""}]);
