import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import ChatChannelAvatar from 'components/ChatChannelAvatar'

export default () => {
  return <div>
    {['sm', 'md', 'lg'].map(size =>
      <Fragment>
        <StyleguideExample
          title={`size=${size}`}
          render={() =>
            <ChatChannelAvatar size={size} value="#" />
          }
        />
        <StyleguideExample
          title={`size=${size} bordered`}
          render={() =>
            <ChatChannelAvatar size={size} value="#" bordered />
          }
        />
      </Fragment>
    )}
  </div>
}
