import { uploadAsset } from 'lib/assets'
export * as location from 'lib/location'

import dismissError from 'lib/dismissError'
export const ui = { dismissError }

export const assets = {
  async uploadDirect(file){
    const response = await global.fetch(
      // the styleguide can safely have the ASSETS_SERVER_SECRET
      // since we do not deploy it anywhere publically
      `${ASSETS_SERVER_URL}/create-upload-token/${ASSETS_SERVER_SECRET}`, // eslint-disable-line no-undef
      {method: 'POST'}
    )
    const assetUploadToken = (await response.json()).uploadToken
    return uploadAsset({ file, assetUploadToken })
  }
}
