import { h } from 'preact'
import { useState } from 'preact/hooks'

import PriceInput from 'components/PriceInput'
import StyleguideExample from 'components/StyleguideExample'

export default () => {
  const [price, setPrice] = useState(12345)
  return <div>
    <div><code>{price}</code></div>
    <StyleguideExample
      title="using onChange"
      render={() =>
        <PriceInput {...{
          value: price,
          onChange: setPrice
        }}/>
      }
    />
    <StyleguideExample
      title="using onInput"
      render={() =>
        <PriceInput {...{
          value: price,
          onInput: setPrice
        }}/>
      }
    />
  </div>
}
