import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import OrganizationListMember from 'components/OrganizationListMember'
import Button from 'components/Button'
import IconButton from 'components/IconButton'
import osho from 'components/OrganizationIcon/examples/osho.png'

export default () => {
  const organization = {
    name: 'Osho International Foundation',
    consumer_icon: osho,
    tag_line: 'We are awake and you can be too!',
  }

  const href = 'https://osho.me'

  const buttons = <Fragment>
    <Button type="primary" value="Visit" href={href}/>
    <IconButton type="share" blue  href={href}/>
  </Fragment>

  return <div>
    <StyleguideExample
      title="default"
      render={() =>
        <OrganizationListMember {...{href, organization, buttons}}/>
      }
    />
    <StyleguideExample
      title="disabled"
      render={() =>
        <OrganizationListMember disabled {...{href, organization, buttons}}/>
      }
    />
  </div>
}
