import { h } from 'preact'
import { useErrorBoundary, useState } from 'preact/hooks'

import history from 'lib/history'
import { AppState, AppStateProvider } from 'lib/appState'
import { Portal } from 'lib/portal'

import { useLocation } from 'resources/location'

import Layout from 'components/Layout'
import SideNav from 'components/SideNav'
import OneColumn from 'components/OneColumn'
import Dropdown from 'components/Dropdown'
import ErrorMessage from 'components/ErrorMessage'

import * as actions from './actions'
import router from './router'
import components from './components'
import { THEMES } from './pages/ThemesPage'
import './App.sass'

const appState = new AppState({
  actions,
  init(){},
})

export default function App(){
  return <AppStateProvider {...{appState}}>
    <AppRoot />
  </AppStateProvider>
}

function AppRoot({ }){
  const location = useLocation()

  const [theme, setTheme] = useState('APortalTheme')

  const Theme = THEMES[theme || 'APortalTheme']

  const { Component, params } = router.resolve(location.pathname)

  const sideNav = <StyleguideSideNav
    theme={theme}
    onThemeChange={setTheme}
    selectedComponent={params.component}
  />

  return <Theme>
    <Layout
      className="Styleguide"
      logout={() => {}}
      sideNav={sideNav}
      currentUser={{}}
    >
      <OneColumn><Router {...{location, Component, params}}/></OneColumn>
    </Layout>
    <Portal/>
  </Theme>
}

function Router({ location, Component, params }){
  const [error] = useErrorBoundary()
  if (error) console.error('Router render error', error)
  if (error) return <ErrorMessage error={error}/>
  return <Component location={location} params={params} />
}

const StyleguideSideNav = props => {
  const {
    theme,
    onThemeChange,
    selectedComponent,
  } = props

  return <SideNav className="SideNav">
    <SideNav.Header>STYLEGUIDE</SideNav.Header>
    <SideNav.Button
      href="/"
      icon="home"
      value="Home"
    />
    <SideNav.Header>THEME</SideNav.Header>
    <Dropdown
      className="Styleguide-ThemeDropdown"
      options={Object.keys(THEMES)}
      value={theme}
      onChange={onThemeChange}
    />
    <SideNav.Header>COMPONENTS</SideNav.Header>
    <Dropdown
      className="Styleguide-ComponentDropdown"
      options={Object.keys(components).sort()}
      value={selectedComponent}
      onChange={component => {
        history.visit(`/components/${component}`)
      }}
      type="normal"
    />
    <SideNav.Header>PAGES</SideNav.Header>
    <SideNav.Button
      href="/text"
      icon="credit-card"
      value="Text"
    />
    <SideNav.Button
      href="/components/FormBuilder"
      icon="spaces"
      value="Forms"
    />
    <SideNav.Button
      href="/components"
      icon="credit-card"
      value="Components"
    />
    <SideNav.Button
      href="/components/Icon"
      icon="vimeo"
      value="Icons"
    />
    <SideNav.Button
      href="/colors"
      icon="network"
      value="Colors"
    />
    <SideNav.Button
      href="/errors"
      icon="cancel-circled"
      value="Errors"
    />
    <SideNav.Button
      href="/themes"
      icon="cog"
      value="Themes"
    />
    <SideNav.Button
      href="/app-state-test"
      icon="cog"
      value="AppState Test"
    />
  </SideNav>
}
