import { h } from 'preact'
import { useReducer } from 'preact/hooks'

import StyleguideExample from 'components/StyleguideExample'
import NotificationBadge from 'components/NotificationBadge'
import Button from 'components/Button'
import IconButton from 'components/IconButton'
import Icon from 'components/Icon'

export default () => {
  const [number, change] = useReducer((n, d) => n + d, 0)
  return <div>
    <style>
      {`
        .NotificationBadge-examples-button{
          position: relative;
        }
        .NotificationBadge-examples-button .NotificationBadge{
          position: absolute;
          top: -10px;
          right: -10px;
        }
      `}
    </style>
    <Button type="normal"onClick={() => { change(-number) }} value="reset"/>
    <Button type="normal"onClick={() => { change(-1) }} value="-1"/>
    <Button type="normal"onClick={() => { change(1) }} value="+1"/>
    <StyleguideExample
      title="in a Button"
      render={() =>
        <Button
          type="normal"
          className="NotificationBadge-examples-button"
          onClick={() => { change(1) }}
        >
          <span>click me</span>
          <NotificationBadge total={number}/>
        </Button>
      }
    />
    <StyleguideExample
      title="in an IconButton"
      render={() =>
        <IconButton
          className="NotificationBadge-examples-button"
          onClick={() => { change(1) }}
        >
          <Icon type="notification-bell"/>
          <NotificationBadge total={number}/>
        </IconButton>
      }
    />
  </div>
}
