import { h } from 'preact'

import Link from 'components/Link'
import StyleguideExample from 'components/StyleguideExample'
import PageTabs from 'components/PageTabs'

export default () => {
  const pathPrefix = '/components/PageTabs'
  const tabs = [
    {
      title: 'Root',
      href: pathPrefix,
      render: () => <div>root tab</div>
    },
    {
      title: 'Trunk',
      href: pathPrefix + '/trunk',
      render: () => <div>/trunk sub page</div>
    },
    {
      title: 'Branch',
      href: pathPrefix + '/trunk/branch',
      route: pathPrefix + '/trunk/branch/:path*',
      render: () => <div>
        <div>/trunk/branch sub page</div>
        <Link type="link" href={pathPrefix + '/trunk/branch/leaf'}>go to leaf</Link>
      </div>
    },
  ]
  return <div>
    <StyleguideExample
      title="default"
      render={() =>
        <PageTabs {...{tabs}} />
      }
    />
    <StyleguideExample
      title="disabled"
      render={() =>
        <PageTabs disabled {...{tabs}} />
      }
    />
  </div>
}
