import { h } from 'preact'
import { useState } from 'preact/hooks'

import Button from 'components/Button'
import ErrorMessage from 'components/ErrorMessage'

const error1 = new Error(`example error 1`)
const typeError = new TypeError(`arg 1 should be a string`)

export default () =>
  <div>
    <ErrorMessage error={error1}/>
    <ErrorMessage error={typeError} dismissable={false}/>
    <ErrorMaker />
  </div>


const ErrorMaker = () => {
  const [error, setError] = useState()
  return <div>
    <ErrorMessage error={error} onDismiss={() => { setError() }}/>
    <Button type="primary" onClick={() => { setError(typeError) }}>Make Error</Button>
  </div>
}
