import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import EndUserAvatar from 'components/EndUserAvatar'
import EndUserAvatarStack from 'components/EndUserAvatarStack'
import example1 from 'components/EndUserAvatar/example.jpg'
import example2 from 'components/EndUserAvatar/example2.jpg'
import example3 from 'components/EndUserAvatar/example3.png'
import example4 from 'components/EndUserAvatar/example4.png'
import example5 from 'components/EndUserAvatar/example5.png'

const publicProfiles = [
  {avatar: example1 },
  {avatar: example3 },
  {avatar: example4 },
  {avatar: example5 },
  {avatar: example2 },
]

export default () => {
  return <div>
    {EndUserAvatar.SIZES.map(size =>
      <StyleguideExample
        title={`size=${size}`}
        render={() =>
          <Fragment>
            {[0, 1, 2, 3, 4].map(length =>
              <EndUserAvatarStack {...{
                publicProfiles: [...publicProfiles].slice(0, length),
                size,
              }}/>
            )}
          </Fragment>
        }
      />
    )}

    <StyleguideExample
      title={`with broken images`}
      render={() =>
        <EndUserAvatarStack {...{
          size: 'lg',
          publicProfiles: [
            {avatar: example2 },
            {avatar: 'https://localhost:1234/not-going-to-load.jpeg' },
          ],
        }}/>
      }
    />
  </div>
}
