import { h } from 'preact'
import { useState } from 'preact/hooks'
import delay from 'lib/delay'
import Page from 'lib/Page'
import {
  AppState,
  AppStateProvider,
  useAppState,
  bindToAppState,
} from 'lib/appState'

import InspectObject from 'components/InspectObject'

export default class AppStateTestPage extends Page {

  static pageTitle = 'AppState Test - Styleguide'

  render() {
    return <div>
      <AppStateTest key={0} index={0} />
      <hr />
      <AppStateTest key={1} index={1} />
    </div>
  }
}

function AppStateTest(props){
  const init = () => {

  }
  const appState = new AppState({actions, init})
  return <AppStateProvider {...{appState}}>
    <h1>AppState Test #{props.index}</h1>
    <Chest/>
    <FavoriteColorSelector/>
    <PersonalDataForm userId={0} />
    <PersonalDataForm userId={4} />
    <PersonalDataFilmstrip />
    <ToggleTreeFrog />
    <DoMultipleUserAppStatesCauseMultipleRenders />
    <InspectAppState/>
  </AppStateProvider>
}

const actions = {
  resetAppState(){
    this.resetState()
  },
  openChest(){
    this.setState({chestOpen: true})
  },
  closeChest(){
    this.setState({chestOpen: undefined})
  },
  setFavoriteColor(favoriteColor){
    if (favoriteColor === '') favoriteColor = undefined
    this.setState({ favoriteColor })
  },
  setPersonalData({ userId, firstName, lastName }){
    this.setState({
      [`user:${userId}:firstName`]: firstName,
      [`user:${userId}:lastName`]: lastName,
    })
  },
  setPersonalDataFilmstripUserId(userId){
    this.setState({
      personalDataFilmstripUserId: userId,
    })
  },
  toggleTreeFrog(){
    this.setState({
      treeFrogVisible: !this.getState().treeFrogVisible,
    })
  },
  loadTreeFrog(){
    if (this.getState().loadingTreeFrog) return
    this.setState({ loadingTreeFrog: true })
    delay(3000).then(() => {
      this.setState({
        loadingTreeFrog: undefined,
        treeFrog: 'https://external-content.duckduckgo.com/iu/?u=http%3A%2F%2Fupload.wikimedia.org%2Fwikipedia%2Fcommons%2Fb%2Fbe%2FRed_eyed_tree_frog_edit2.jpg&f=1&nofb=1',
      })
    })
  }
}

function InspectAppState(){
  const { takeAction, appAction, ...appState } = useAppState(Infinity)
  takeAction // removed from appState
  return <div>
    <h3>AppState:</h3>
    <InspectObject object={appState} />
    <button onClick={appAction('resetAppState')}>reset appState</button>
  </div>
}


function Chest(){
  const { chestOpen, appAction } = useAppState(['chestOpen'])
  return <div>
    <button onClick={appAction('openChest')}>open</button>
    <button onClick={appAction('closeChest')}>close</button>
    &nbsp;<span>Chest is {chestOpen ? 'open' : 'closed' }</span>
  </div>
}


const COLORS = Object.freeze(['red', 'organge', 'yellow', 'blue'])
const FavoriteColorSelector = bindToAppState(
  ['favoriteColor'],
  ({ favoriteColor, takeAction }) => {
    return <div>
      <select
        value={favoriteColor || ''}
        onChange={event => {
          takeAction('setFavoriteColor', event.target.value)
        }}
      >
        <option value="">select a color</option>
        {COLORS.map(color =>
          <option value={color}>{color}</option>
        )}
      </select>
    </div>
  },
)


function PersonalDataForm({ userId }){
  const {
    takeAction,
    firstName = '',
    lastName = '',
  } = useAppState({
    [`user:${userId}:firstName`]: 'firstName',
    [`user:${userId}:lastName`]: 'lastName',
  })
  const onChange = key =>
    event => {
      takeAction('setPersonalData', {
        userId,
        firstName: firstName,
        lastName: lastName,
        [key]: event.target.value,
      })
    }

  return <form>
    <h3>Personal Data for User {userId}</h3>
    <input
      placeholder="First Name"
      value={firstName}
      onChange={onChange('firstName')}
    />
    <input
      placeholder="Last Name"
      value={lastName}
      onChange={onChange('lastName')}
    />
  </form>
}

function PersonalDataFilmstrip(){
  const {
    takeAction,
    personalDataFilmstripUserId: userId = 0,
  } = useAppState([
    'personalDataFilmstripUserId',
  ])
  const prev = () => { takeAction('setPersonalDataFilmstripUserId', userId - 1) }
  const next = () => { takeAction('setPersonalDataFilmstripUserId', userId + 1) }

  return <div>
    <h3>Personal Data Filmstrip</h3>
    <div>
      <button onClick={prev}>prev</button>
      <button onClick={next}>next</button>
    </div>
    <PersonalDataForm userId={userId} />
  </div>
}

class TreeFrog {
  componentDidMount(){
    this.props.takeAction('loadTreeFrog')
  }
  render(){
    const { loadingTreeFrog, treeFrog } = this.props
    return <div>
      {loadingTreeFrog && <div>Loading Tree Frog</div> }
      {treeFrog &&
        <div>
          <div>Look a tree frog:</div>
          <img src={treeFrog} height={20}/>
        </div>
      }
    </div>
  }
}

const BoundTreeFrog = bindToAppState(
  [
    'loadingTreeFrog',
    'treeFrog',
  ],
  TreeFrog,
  // props => {
  //   props.takeAction('loadTreeFrog')
  // }
)


function ToggleTreeFrog(){
  const { treeFrogVisible, takeAction } = useAppState(['treeFrogVisible'])
  const toggle = () => { takeAction('toggleTreeFrog') }
  const helpText = `
    This example illustrates an edge-case issue. When TreeFrog mounts it takes
    the action 'loadTreeFrog' which sets the appState key loadingTreeFrog to true.
    The bug is that this appState change is published before TreeFrog is bound to
    appState changes and so its never re-rendered with the change to loadingTreeFrog.
  `
  return <div>
    <h3>Toggle Tree Frog <span title={helpText}>?</span></h3>
    <div>
      <button onClick={toggle}>{treeFrogVisible ? 'hide' : 'show'}</button>
    </div>
    {treeFrogVisible && <BoundTreeFrog />}
  </div>
}

DoMultipleUserAppStatesCauseMultipleRenders.renderCounts = {}
function DoMultipleUserAppStatesCauseMultipleRenders(){
  const [uuid] = useState(Math.random())
  DoMultipleUserAppStatesCauseMultipleRenders.renderCounts[uuid] = (
    (DoMultipleUserAppStatesCauseMultipleRenders.renderCounts[uuid] || 0) + 1
  )
  console.warn('renderCount', DoMultipleUserAppStatesCauseMultipleRenders.renderCounts[uuid])

  const { takeAction } = useAppState(undefined, 'DoMultipleUserAppStatesCauseMultipleRenders')

  const userIds = Array(10).fill().map((_, i) => i)

  const firstNames = useAppState(
    userIds.map(userId => `user:${userId}:firstName`)
  )

  const lastNames = useAppState(
    userIds.map(userId => `user:${userId}:lastName`)
  )

  const users = userIds.map(userId => {
    const firstName = firstNames[`user:${userId}:firstName`]
    const lastName = lastNames[`user:${userId}:lastName`]
    return `${userId}: ${firstName} ${lastName}`
  })

  console.warn('RENDER', {
    uuid,
    count: DoMultipleUserAppStatesCauseMultipleRenders.renderCounts[uuid],
    firstNames,
    lastNames,
  })

  function multipleKeyChanges(){
    takeAction('setPersonalData', {
      userId: Math.round(Math.random() * 10),
      firstName: `${Math.random()}`,
      lastName: `${Math.random()}`,
    })
    takeAction('setPersonalData', {
      userId: Math.round(Math.random() * 10),
      firstName: `${Math.random()}`,
      lastName: `${Math.random()}`,
    })
  }

  return <div>
    <div>render count: {DoMultipleUserAppStatesCauseMultipleRenders.renderCounts[uuid]}</div>
    <button onClick={multipleKeyChanges}>set mutliple key changes</button>
    {users.map(user => <div>{user}</div>)}
  </div>
}
