import { h } from 'preact'

import useToggle from 'lib/useToggleHook'

import SlideInAnimation from 'components/SlideInAnimation'

export default () => {
  const [oneOpen, , , toggleOne] = useToggle(false)
  const [twoOpen, , , toggleTwo] = useToggle(false)

  const catImage = <img
    src="https://pbs.twimg.com/profile_images/996948456160641024/VXc2Fbge.jpg"
    height="400"
  />

  return <div className="SlideInAnimationExamples">
    <style>{`
      .SlideInAnimationExamples .SlideInAnimation{
        border: 1px dotted green;
      }
    `}</style>

    <button onClick={toggleOne}>toggle</button>
    <SlideInAnimation>
      {oneOpen && catImage}
    </SlideInAnimation>

    <button onClick={toggleTwo}>toggle</button>
    <SlideInAnimation>
      {twoOpen &&
        <p>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed
          do eiusmod tempor incididunt ut labore et dolore magna aliqua.
          Ut enim ad minim veniam, quis nostrud exercitation ul.
        </p>
      }
    </SlideInAnimation>
  </div>
}
