import { h, Component } from 'preact'
import Page from 'lib/Page'

import Header from 'components/Header'
import Link from 'components/Link'

import components from '../components'

export default class ComponentsPage extends Page {

  getPageTitle(){
    return `${this.props.params.component} - Styleguide`
  }

  render() {
    const { location } = this.props
    const component = (this.props.params.component || '').split('?')[0]

    const content = component
      ? <ComponentExamples {...{location, component}}/>
      : <ComponentList />
    return <div>
      {content}
    </div>
  }
}

class ComponentExamples extends Component {

  getState(component){
    return this.state[component] || {}
  }

  setStateFor(component){
    return (changes, callback) => {
      const newState = { ...this.getState(component), ...changes }
      return this.setState({ [component]: newState }, callback)
    }
  }

  render(){
    const { location, component } = this.props
    const Examples = components[component]

    if (!Examples) return <div>
      <Header size="lg">{component} Not Found!</Header>
      <Header size="lg"> ()  </Header>
      <p>If it exists, make sure it is imported in src/styleguide/components.js</p>
    </div>

    return <div>
      <Header size="lg" underlined>{component} Examples:</Header>
      <Examples
        location={location}
        component={component}
        state={this.getState(component)}
        setState={this.setStateFor(component)}
      />
    </div>
  }
}

const ComponentList = () => {
  const links = Object.keys(components)
    .sort((a, b) => a > b ? 1 : a < b ? -1 : 0)
    .map(name =>
      <li><Link type="link" href={`/components/${name}`}>{name}</Link></li>
    )
  return <div>
    <Header size="lg" underlined>Components:</Header>
    <ul>{links}</ul>
  </div>
}
