export const DECORATED_COMMUNICATION_CHANNEL_TITLES = (() => {
  const value = {...COMMUNICATION_CHANNEL_TITLES}
  ;['google', 'whatsapp', 'instagram', 'facebook'].forEach(key => {
    value[key] += ' '
  })
  ;['twitter', 'linkedin'].forEach(key => {
    value[key] += ' '
  })
  return Object.freeze(value)
})()
