import { h, Fragment } from 'preact'

import { useLocation } from 'resources/location'

import Button from 'components/Button'
import BannerNav from 'components/BannerNav'
import OrganizationIcon from 'components/OrganizationIcon'
import EndUserAvatar from 'components/EndUserAvatar'
import EndUserBanner from 'components/EndUserBanner'
import OrganizationBanner from 'components/OrganizationBanner'
import exampleBanner from 'components/OrganizationBanner/example-banner.jpg'
import exampleOrganizationIcon from 'components/OrganizationIcon/example-jlinc-knot.png'
import exampleEndUserAvatar from 'components/EndUserAvatar/example.jpg'

export default () => {
  const location = useLocation()
  const exampleIsOrganization = location.query.e === 'organization'

  const Example = exampleIsOrganization
    ? OrganzationExample
    : UserExample

  return <div style={{
    '--Layout-TopNav-top': '0px',
    '--Layout-TopNav-height': 'var(--top-nav-height)',
  }}>
    <div style={{marginBottom: '100px'}}>
      <Button
        type="primary"
        href={location.withQuery({e:'organization'})}
        disabled={exampleIsOrganization}
        value="switch to organization example"
      />
      &nbsp;
      <Button
        type="primary"
        href={location.withQuery({e:'user'})}
        disabled={!exampleIsOrganization}
        value="switch to user example"
      />
    </div>
    <Example />

    <PageFiller />
  </div>
}

function OrganzationExample(props){
  const organization = {
    consumer_icon: exampleOrganizationIcon,
    consumer_banner: exampleBanner,
  }
  return <BannerNav {...{
    banner: <OrganizationBanner {...{organization}}/>,
    icon: <OrganizationIcon
      organization={organization}
      size="md"
      bordered
    />,
    name: <div>
      <div>This is where the org name goes</div>
      <div>also a second line</div>
    </div>,
    pathnameRoot: `/components/BannerNav`,
    navButtons: [
      {
        value: 'About',
        icon: 'home',
        path: '',
      },
      {
        value: 'Published',
        icon: 'public-feed',
        path: '/feed',
      },
      {
        value: 'Forum',
        icon: 'channel',
        path: '/channel',
        disabled: true,
      },
      {
        value: 'Connections',
        icon: 'hub-list',
        path: '/connections',
      },
    ],
    ...props
  }}/>
}

function UserExample(props){
  const publicProfile = {
    username: 'shelaburger',
    displayName: 'Shela Burger',
    banner: 'https://assets.jlinc.test/16f23950-d047-11ea-98cc-47feb845fef0.png',
  }
  return <BannerNav {...{
    banner: <EndUserBanner {...{publicProfile}}/>,
    icon: <EndUserAvatar publicProfile={{avatar:exampleEndUserAvatar}} size="md" bordered/>,
    name: <div>
      <div>{publicProfile.displayName}</div>
      <div>@{publicProfile.username}</div>
    </div>,
    pathnameRoot: `/components/BannerNav`,
    navButtons: [
      {
        value: 'Memberships',
        icon: 'hub-list',
        path: '/memberships',
      },
      {
        value: 'Feed',
        icon: 'channel',
        path: '/',
      },
    ],
    ...props
  }}/>
}

const LOREM_WORDS = `
Fugiat enim qui qui dolore voluptate cillum exercitation ut in tempor
enim reprehenderit excepteur laboris excepteur pariatur tempor sint anim
enim est ad esse duis veniam mollit culpa non in.
`.trim().split(' ')
const randomWord = () =>
  LOREM_WORDS[Math.floor(Math.random() * LOREM_WORDS.length)]

function PageFiller(){
  return <Fragment>
    {Array(20).fill().map(() =>
      <p>{Array(Math.floor(Math.random() * 100)).fill().map(randomWord).join(' ')}</p>
    )}
  </Fragment>
}
