import { h } from 'preact'

import Button from 'components/Button'
import Prompt from 'components/Prompt'

export default ({ state, setState }) =>
  <div>

    <Button
      type="primary"
      onClick={() => { setState({confirmingDelete:true}) }}
    >
      DELETE ALL THE THINGS!
    </Button>

    { state.confirmingDelete &&
      <Prompt
        query="Are you sure you want to delete all the things?"
        onConfirmation={() => { setState({confirmingDelete:false}) }}
        onRejection={() => { setState({confirmingDelete:false}) }}
      />
    }

  </div>
