import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import TextInput from 'components/TextInput'

export default ({ state, setState }) => {
  return <Fragment>
    <StyleguideExample
      title="default"
      render={() =>
        <TextInput
          value={state.example1}
          onInput={example1 => { setState({example1}) }}
        />
      }
    />

    <StyleguideExample
      title="with placeholder"
      render={() =>
        <TextInput
          value={state.example2}
          onInput={example2 => { setState({example2}) }}
          placeholder="First Name"
        />
      }
    />

    <StyleguideExample
      title="unsaved"
      render={() =>
        <TextInput
          value={state.example3}
          onInput={example3 => { setState({example3}) }}
          placeholder="Last Name"
          unsaved
        />
      }
    />

    <StyleguideExample
      title="disabled"
      render={() =>
        <TextInput
          value={state.example4}
          onInput={example4 => { setState({example4}) }}
          disabled
          placeholder="First Name"
        />
      }
    />

    <StyleguideExample
      title="with validationError"
      render={() =>
        <TextInput
          value={state.example5}
          onInput={example5 => { setState({example5}) }}
          placeholder="First Name"
          validationError={new Error('cannot be empty')}
        />
      }
    />


    {TextInput.TYPES.map(type =>
      <StyleguideExample
        title={`type=${type}`}
        render={() =>
          <TextInput type={type} />
        }
      />
    )}

  </Fragment>
}
