import { h } from 'preact'
import PropTypes from 'prop-types'

import Button from 'components/Button'
import ButtonGroup from 'components/ButtonGroup'

export default function ButtonGroupInput({
  disabled,
  unsaved,
  value,
  onChange,
  options,
  fat,
  ...props
}){
  let className = 'ButtonGroupInput'
  if (unsaved) className += ` ButtonGroupInput-unsaved`
  if (props.className) className += ` ${props.className}`
  return <ButtonGroup {...{...props, className}}>
    {options.map(option => {
      if (typeof option === 'string')
        option = { value: option, buttonValue: option }
      return <Button {...{
        fat,
        disabled: disabled || option.disabled,
        type: option.value === value
          ? unsaved ? 'success' : 'primary'
          : 'normal',
        onClick: () => { onChange(option.value) }
      }}>{option.buttonValue}</Button>
    })}
  </ButtonGroup>
}

ButtonGroupInput.propTypes = {
  className: PropTypes.string,
  fat: Button.propTypes.fat,
  disabled: Button.propTypes.disabled,
  unsaved: Button.bool,
  value: PropTypes.any, // eslint-disable-line react/forbid-prop-types
  onChange: PropTypes.func.isRequired,
  options: PropTypes.arrayOf(
    PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.shape({
        value: PropTypes.string,
        buttonValue: PropTypes.node,
        disabled: PropTypes.bool,
      }),
    ])
  ).isRequired,
}
