import moment from 'moment'
import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import CountdownToTime from 'components/CountdownToTime'

const LOADED_AT = moment()
const DURS = 'seconds minutes hours days weeks months years'.split(' ')

export default () => {
  return <div>
    {
      DURS.map(dur =>
        <StyleguideExample
          title={`two ${dur} from page load time`}
          render={() =>
            <CountdownToTime to={LOADED_AT.clone().add(2, dur)} />
          }
        />
      )
    }
  </div>
}
