import { h } from 'preact'
import { useState } from 'preact/hooks'

import Header from 'components/Header'
import WizardSideNav from 'components/WizardSideNav'

export default ({ }) => {
  const [selectedStepIndex, setSelectedStepIndex] = useState(3)
  const [completedSteps, setCompletedSteps] = useState(new Set([0, 1, 2]))
  const [disabledSteps, setDisabledSteps] = useState(new Set([7, 8, 9]))

  const steps = Array(10).fill().map((_, index) => {
    return {
      title: `Step ${index}`,
      selected: selectedStepIndex === index,
      disabled: disabledSteps.has(index),
      complete: completedSteps.has(index),
      onClick: () => { setSelectedStepIndex(index) },
    }
  })

  return <div>
    <div>
      <Header size="md">State:</Header>
      {steps.map((step, index) =>
        <div>
          Disabled:
          <input {...{
            type: 'checkbox',
            checked: disabledSteps.has(index),
            onChange: checked => {
              disabledSteps[checked ? 'add' : 'delete'](index)
              setDisabledSteps(new Set(disabledSteps))
            },
          }}/>
          Complete:
          <input {...{
            type: 'checkbox',
            checked: completedSteps.has(index),
            onChange: checked => {
              completedSteps[checked ? 'add' : 'delete'](index)
              setCompletedSteps(new Set(completedSteps))
            },
          }}/>
        </div>
      )}
    </div>
    <WizardSideNav {...{
      steps,
      disabled: false
    }}/>
  </div>
}
