import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import InspectObject from 'components/InspectObject'
import DropdownPicker from 'components/DropdownPicker'

const COLORS = ['Red', 'Orange', 'Yellow', 'Blue', 'Green']

export default ({ state, setState }) =>
  <div>
    <StyleguideExample
      title="standard"
      render={() =>
        <DropdownPicker
          options={COLORS}
          value={state.color}
          onChange={color => setState({ color })}
          placeholder="favorite color"
        />
      }
    />
    <StyleguideExample
      title="disabled"
      render={() =>
        <DropdownPicker
          options={COLORS}
          value={state.color}
          onChange={color => setState({ color })}
          placeholder="favorite color"
          disabled
        />
      }
    />
    <StyleguideExample
      title="allowing custom values"
      render={() =>
        <DropdownPicker
          options={COLORS}
          value={state.color}
          onChange={color => { setState({ color }) }}
          placeholder="select your favorite color"
          allowCustom
        />
      }
    />

    <StyleguideExample
      title="with defaultValue"
      render={() =>
        <DropdownPicker
          options={COLORS}
          value={state.color2}
          onChange={color2 => { setState({ color2 }) }}
          placeholder="select your favorite color"
          defaultValue="Red"
        />
      }
    />
    <InspectObject object={state} />
  </div>
