import { h, Fragment } from 'preact'

import HugeChoiceInput from 'components/HugeChoiceInput'
import Icon from 'components/Icon'

export default ({ state, setState }) =>
  <div>
    <p>value: {state.example1}</p>
    <HugeChoiceInput
      options={[
        {
          value: 'iphone',
          buttonValue: 'iPhone',
          description: <Fragment>
            <p>Is there really any other choice?</p>
            <ul>
              <li>Best is class</li>
              <li>Slick A.F.</li>
            </ul>
          </Fragment>,
        },
        {
          value: 'android',
          buttonValue: 'Android',
          description: <Fragment>
            <p>Advantages of an Andriod:</p>
            <ul>
              <li>Robots are rad</li>
              <li>Google be watch'n</li>
            </ul>
          </Fragment>,
        },
        {
          value: 'stick',
          buttonValue: 'Stick Phone',
          description: <Fragment>
            <p>The advantages of a Stick Phone:</p>
            <ul>
              <li>Doubles as a back scratcher</li>
              <li>Stick included!</li>
            </ul>
          </Fragment>,
        },
      ]}
      value={state.example1}
      onChange={example1 => { setState({ example1 }) }}
    />
    <p>value: {state.example2}</p>
    <HugeChoiceInput
      options={
        Icon.types.slice(1, 7).map(type => {
          return {
            value: type,
            buttonValue: type,
            description: <div style={{textAlign: 'center'}}>
              <Icon {...{type}}/>
            </div>,
            disabled: type === Icon.types[3],
          }
        })
      }
      value={state.example2 || Icon.types[5]}
      unsaved={state.example2 && state.example2 !== Icon.types[5]}
      onChange={example2 => { setState({ example2 }) }}
    />
  </div>
