import { h } from 'preact'

import './index.sass'

export default ({ source = '' }) =>
  <code className="Code">{outdent(source)}</code>

const outdent = source => {
  source = source.replace(/^\n+/, '')
  const length = source.toString().match(/^(\s*)/)[1].length
  const regExp = new RegExp(`^\\s{${length}}`, 'gm')
  return source.replace(regExp, '').trim()
}
