import { h } from 'preact'

import useToggle from 'lib/useToggleHook'

import StyleguideExample from 'components/StyleguideExample'
import BounceArrow from 'components/BounceArrow'

export default () => {
  const [active, , , toggle] = useToggle()
  return <div>
    <div>
      Active: <input type="checkbox" onClick={toggle} checked={active}/>
    </div>
    <div>
      <BounceArrow up active={active} />
      <BounceArrow down active={active} />
    </div>
    <StyleguideExample
      title="up"
      render={() =>
        <BounceArrow up />
      }
    />
    <StyleguideExample
      title="up active"
      render={() =>
        <BounceArrow up active />
      }
    />
    <StyleguideExample
      title="down"
      render={() =>
        <BounceArrow down />
      }
    />
    <StyleguideExample
      title="down active"
      render={() =>
        <BounceArrow down active />
      }
    />
    <StyleguideExample
      title="up active"
      render={() =>
        <BounceArrow up active />
      }
    />
    <StyleguideExample
      title="up disabled"
      render={() =>
        <BounceArrow up disabled />
      }
    />
  </div>
}
