import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import InspectObject from 'components/InspectObject'
import CountryDropdownPicker from 'components/CountryDropdownPicker'

export default ({ state, setState }) =>
  <div>
    <StyleguideExample
      title="standard"
      render={() =>
        <CountryDropdownPicker
          onChange={country => { setState({ country }) }}
          value={state.country}
          placeholder="select country"
        />
      }
    />

    <StyleguideExample
      title="disabled"
      render={() =>
        <CountryDropdownPicker
          onChange={country => { setState({ country }) }}
          value={state.country}
          placeholder="select country"
          disabled
        />
      }
    />

    <StyleguideExample
      title="with default"
      render={() =>
        <CountryDropdownPicker
          onChange={country => { setState({ country }) }}
          value={state.country}
          placeholder="select country"
          defaultValue="United States"
        />
      }
    />

    <InspectObject object={state} />
  </div>
