import { h, Fragment } from 'preact'

import useToggle from 'lib/useToggleHook'

import Button from 'components/Button'
import StyleishModal from 'components/StyleishModal'
import Tooltip from 'components/Tooltip'

export default () => {
  const [isScrollsInternallyOpen, openScrollsInternally, closeScrollsInternally] = useToggle(false)
  const [isScrollsExternallyOpen, openScrollsExternally, closeScrollsExternally] = useToggle(false)
  const [isModalWithErrorOpen, openModalWithError, closeModalWithError] = useToggle(false)
  const [isModalWithAModalOpen, openModalWithAModal, closeModalWithAModal] = useToggle(false)
  const [isNestedModalOpen, openNestedModal, closeNestedModal] = useToggle(false)

  const content = <Fragment>
    {Array(10).fill().map(() =>
      <p>
        Dolor nostrud sint cupidatat adipisicing pariatur cupidatat aliquip aliqua
        consectetur sint dolore enim excepteur et exercitation voluptate quis labore
        <Tooltip text="this is some tooltop content"> THIS TEXT HAS A TOOLTIP </Tooltip>
        tempor ex velit eiusmod commodo sed eu commodo pariatur adipisicing
        consectetur in nostrud mollit sunt proident et sit laborum aliquip ut irure ut
        qui duis incididunt est pariatur dolor ad dolor dolore dolore minim et
      </p>
    )}
  </Fragment>

  return <div>
    <p><Button type="primary" value="scrolls externally" onClick={openScrollsExternally} /></p>
    <p><Button type="primary" value="scrolls internally" onClick={openScrollsInternally} /></p>
    <p><Button type="primary" value="has a render error" onClick={openModalWithError} /></p>
    <p><Button type="primary" value="nested modals" onClick={openModalWithAModal} /></p>

    <StyleishModal
      title="Example of a <StyleishModal />"
      open={isScrollsExternallyOpen}
      onClose={closeScrollsExternally}
    >{content}</StyleishModal>

    <StyleishModal
      title="Example of a <StyleishModal scrollInternally />"
      open={isScrollsInternallyOpen}
      onClose={closeScrollsInternally}
      scrollInternally
    >{content}</StyleishModal>

    <StyleishModal
      title="Example of modal with a render error"
      open={isModalWithErrorOpen}
      onClose={closeModalWithError}
      scrollInternally
    >
      <ComponentWithError />
    </StyleishModal>

    <StyleishModal
      title="Example of modal with another modal"
      open={isModalWithAModalOpen}
      onClose={closeModalWithAModal}
      scrollInternally
    >
      <p>
        <Tooltip text="portals are harder than I expected">
          <Button type="primary" value="open another modal" onClick={openNestedModal} />
        </Tooltip>
      </p>

      <StyleishModal
        title="Another Modal!?!?"
        open={isNestedModalOpen}
        onClose={closeNestedModal}
        scrollInternally
      >
        <Tooltip text="">
          Russian modals are hot
        </Tooltip>
      </StyleishModal>
    </StyleishModal>
  </div>
}


function ComponentWithError(){
  throw new Error('omg this component is broken')
}
