import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import SwitchListMember from 'components/SwitchListMember'

export default ({ state, setState }) => {
  return <Fragment>
    <StyleguideExample
      title="default"
      render={() =>
        <div>
          {[
            'I want tea',
            'I dont like to drink coffee',
            'Underestimated',
            'Tested positive for COVID-19',
            'Loves JLINC',
          ].map((label, index) =>
            <SwitchListMember
              label={label}
              description={`Description for ${label}`}
              value={state[`switch${index}`]}
              onChange={checked => { setState({[`switch${index}`]: checked}) }}
            />
          )}
        </div>
      }
    />
  </Fragment>
}
