import { h } from 'preact'
import { useState, useRef } from 'preact/hooks'

import StyleguideExample from 'components/StyleguideExample'
import Button from 'components/Button'
import TextInput from 'components/TextInput'
import ContextMenu from 'components/ContextMenu'

export default () => {
  const [selectedColor, setSelectedColor] = useState()

  const [example1Open, setExample1Open] = useState()
  const example1Ref = useRef()
  const [example2Open, setExample2Open] = useState()
  const example2Ref = useRef()
  const [example3Open, setExample3Open] = useState()
  const example3Ref = useRef()
  const [example4Open, setExample4Open] = useState()
  const [example4Size, setExample4Size] = useState(1)
  const example4Ref = useRef()
  const [example5Open, setExample5Open] = useState()
  const example5Ref = useRef()

  const options = COLORS.map(color => ({
    value: color,
    onSelect: () => { setSelectedColor(color) },
  }))
  options.splice(2, 0, '--')

  return <div>
    <p style={{
      backgroundColor: selectedColor,
      padding: '10px',
    }}>selected color: {selectedColor}</p>

    <StyleguideExample
      title={`default`}
      render={() =>
        <div>
          <Button
            type="normal"
            ref={example1Ref}
            onClick={() => { setExample1Open(true)}}
          >example 1</Button>
          <ExampleContextMenu {...{
            open: example1Open,
            onClose: () => { setExample1Open(false) },
            anchorRef: example1Ref,
            setSelectedColor,
          }}/>
        </div>
      }
    />

    <StyleguideExample
      title={`rightAligned`}
      render={() =>
        <div>
          <Button
            type="normal"
            ref={example2Ref}
            onClick={() => { setExample2Open(true)}}
          >example 2</Button>
          <ExampleContextMenu {...{
            open: example2Open,
            onClose: () => { setExample2Open(false) },
            anchorRef: example2Ref,
            setSelectedColor,
            rightAligned: true,
          }}/>
        </div>
      }
    />


    <StyleguideExample
      title={`rightAligned`}
      render={() =>
        <div>
          <TextInput
            value={selectedColor}
            onChange={e => { e.preventDefault(); return false}}
            type="normal"
            ref={example3Ref}
            onFocus={() => { setExample3Open(true)}}
            placeholder="example 3"
          />
          <ExampleContextMenu {...{
            open: example3Open,
            onClose: () => { setExample3Open(false) },
            anchorRef: example3Ref,
            setSelectedColor,
          }}/>
        </div>
      }
    />


    <StyleguideExample
      title={`magically repositions after content changes`}
      render={() =>
        <div>
          <Button
            type="normal"
            ref={example4Ref}
            onClick={() => { setExample4Open(true)}}
          >example 4</Button>
          <ContextMenu rightAligned {...{
            open: example4Open,
            onClose: () => { setExample4Open(false) },
            anchorRef: example4Ref,
          }}>
            <div>
              <div>
                <Button
                  style={{float:'right'}}
                  type="normal"
                  onClick={() => { setExample4Size(example4Size + 2) }}
                >MORE!!</Button>
              </div>
              {Array(example4Size).fill().map((_, r) =>
                <div>
                  <span>{r}:</span>
                  {Array(example4Size).fill().map((_, c) =>
                    <span>{c}</span>
                  )}
                </div>
              )}
            </div>
          </ContextMenu>
        </div>
      }
    />


    <StyleguideExample
      title={`extremely long list`}
      render={() =>
        <div>
          <Button
            type="normal"
            ref={example5Ref}
            onClick={() => { setExample5Open(true)}}
          >example 5</Button>
          <ContextMenu {...{
            open: example5Open,
            onClose: () => { setExample5Open(false) },
            anchorRef: example5Ref,
            setSelectedColor,
          }}>
            <div>
              {Array(100).fill().map(() =>
                <p>
                  Dolore duis incididunt quis dolor mollit ex nisi
                  voluptate esse amet est ullamco consequat dolore
                  ex amet dolor non est mollit in ullamco occaecat
                  ea sunt cillum ad anim consequat labore elit in.
                </p>
              )}
            </div>
          </ContextMenu>
        </div>
      }
    />

    {Array(100).fill().map(() => <br/>)}

  </div>
}


function ExampleContextMenu({ setSelectedColor, ...props }){
  return <ContextMenu {...props}>
    {COLORS.map(color =>
      <div>
        <div style={{
          display: 'inline-block',
          backgroundColor: color,
          height: '10px',
          width: '10px',
          margin: '10px',
        }}/>
        <Button
          type="normal"
          onClick={() => { setSelectedColor(color); props.onClose() }}
        >{color}</Button>
      </div>
    )}
  </ContextMenu>
}


