import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import TextInputWithPrefix from 'components/TextInputWithPrefix'
import Form from 'components/Form'
import TextInput from 'components/TextInput'
import InspectObject from 'components/InspectObject'

export default ({ state, setState }) => {
  return <Fragment>

    <InspectObject object={state} />

    <StyleguideExample
      title="default"
      render={() =>
        <TextInputWithPrefix
          value={state.example1}
          onInput={example1 => { setState({example1}) }}
          prefix="https://example.com/org/"
          placeholder="myorgname"
        />
      }
    />

    <StyleguideExample
      title="narrow"
      render={() =>
        <div style={{width: '210px'}}>
          <TextInputWithPrefix
            value={state.example2}
            onInput={example2 => { setState({example2}) }}
            prefix="https://example.com/org/"
            placeholder="myorgname"
          />
        </div>
      }
    />

    <StyleguideExample
      title="minLength=3 and maxLength=12"
      render={() =>
        <Form>
          <p>try a 1 or 2 letter name and see the form error</p>
          <Form.Row>
            <Form.Item>
              <Form.Label>FIRST NAME</Form.Label>
              <TextInput type="text" />
            </Form.Item>
            <Form.Item>
              <Form.Label>LAST NAME</Form.Label>
              <TextInputWithPrefix
                value={state.example1}
                onInput={example1 => { setState({example1}) }}
                prefix="Lord "
                placeholder="Kensington"
                minLength="3"
                maxLength="12"
                required
              />
            </Form.Item>
          </Form.Row>
        </Form>
      }
    />


    <StyleguideExample
      title="default"
      render={() =>
        <TextInputWithPrefix
          value={state.example1}
          onInput={example1 => { setState({example1}) }}
          prefix="@"
          placeholder="PewdiePie"
        />
      }
    />
  </Fragment>
}
