import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import TextInputWithSwitch from 'components/TextInputWithSwitch'
import Icon from 'components/Icon'

export default ({ state, setState }) => {
  return <Fragment>
    <StyleguideExample
      title="on the right"
      render={() =>
        <TextInputWithSwitch
          value={state.example1Text}
          onChange={example1Text => { setState({example1Text}) }}
          checked={state.example1Checked}
          onSwitchChange={example1Checked => { setState({example1Checked}) }}
          switchTooltip
          tooltipText="example switch tooltip"
          icon={<Icon type="calendar" size="sm" />}
          position="right"
          placeholder="First Name"
        />
      }
    />

    <StyleguideExample
      title="switchDisabled"
      render={() =>
        <TextInputWithSwitch
          value={state.example2Text}
          onChange={example2Text => { setState({example2Text}) }}
          checked={state.example2Checked}
          onSwitchChange={example2Checked => { setState({example2Checked}) }}
          switchDisabled
          placeholder="Last Name"
        />
      }
    />

    <StyleguideExample
      title="switchUnsaved"
      render={() =>
        <TextInputWithSwitch
          value={state.example3Text}
          onChange={example3Text => { setState({example3Text}) }}
          checked={state.example3Checked}
          onSwitchChange={example3Checked => { setState({example3Checked}) }}
          position="left"
          placeholder="Size"
          switchUnsaved
        />
      }
    />
  </Fragment>
}
