import { h, Fragment } from 'preact'
import Link from 'components/Link'

export default ({ location }) => {
  const count = parseInt(location && location.query.count || 0, 10)
  return <div>
    {Link.TYPES.map(type =>
      <Fragment>
        <p><Link {...{type, href: './'}}>type={type}</Link></p>
        <p><Link {...{type, href: './'}} disabled>type={type} disabled</Link></p>
        <p><Link {...{type}} disabled>type={type} disabled (no href or onClick)</Link></p>
        <p><Link {...{type}} pathname="/login" query={{r:'/blatentquerk'}}>magic</Link></p>
        <p><Link {...{type}} href={{...location}}>copy of current location</Link></p>
        <p><Link {...{type, href: './'}} newWindow>new window</Link></p>
        <p>
          <Link
            {...{type}}
            href={{query: { count: count + 1 }}}
          >counter ({count})</Link>
        </p>
      </Fragment>
    )}
  </div>
}
