import { h } from 'preact'

import RadioInputGroup from 'components/RadioInputGroup'
import StyleguideExample from 'components/StyleguideExample'

export default ({ state, setState }) =>
  <div>
    <StyleguideExample
      title="options as array of strings with duplicate values"
      render={() =>
        <RadioInputGroup
          value={state.colorIndex}
          onChange={colorIndex => setState({ colorIndex })}
          options={['red', 'yellow', 'blue', 'brown', 'brown']}
          placeholder="choose color"
        />
      }
    />
    <StyleguideExample
      title="options as array objects with label and disabled"
      render={() =>
        <RadioInputGroup
          value={state.animalIndex}
          onChange={animalIndex => setState({ animalIndex })}
          options={[
            {
              label: <i>bear</i>,
            },
            {
              label: <b>giraffe</b>,
            },
            {
              label: <span>tiger</span>,
              disabled: true,
            }
          ]}
          placeholder="choose animal"
        />
      }
    />
    <StyleguideExample
      title="size lg w/ subtexts"
      render={() =>
        <RadioInputGroup
          value={state.colorIndex}
          onChange={colorIndex => setState({ colorIndex })}
          options={[
            {
              label: 'red',
              subtext: 'red is the best color in the world'
            },
            'yellow',
            'blue',
            {
              label: 'brown',
              subtext: 'brown is the color of God'
            },
          ]}
          placeholder="choose color"
          large
        />
      }
    />
  </div>
