import { h } from 'preact'
import useToggle from 'lib/useToggleHook'

import StyleguideExample from 'components/StyleguideExample'
import FatSubmitButton from 'components/FatSubmitButton'

export default () => {
  const [submitting, , , toggle] = useToggle()
  const props = {
    submitting,
    value: ['Open', 'Opening'],
  }
  return <div>
    <button onClick={toggle}>
      {submitting ? 'disable' : 'enabled'} submitting
    </button>
    <StyleguideExample
      title="default"
      render={() =>
        <FatSubmitButton {...props}/>
      }
    />
    <StyleguideExample
      title="disabled"
      render={() =>
        <FatSubmitButton {...props} disabled />
      }
    />
  </div>
}
