import { h, Fragment } from 'preact'

import JLINC_LOGO from './JLINC_LOGO_EXAMPLE.txt'

import StyleguideExample from 'components/StyleguideExample'
import IconRow from 'components/IconRow'
import OrganizationNameAndTagLine from 'components/OrganizationNameAndTagLine'
import OrganizationIcon from 'components/OrganizationIcon'
import Button from 'components/Button'

export default () => {

  const jlincIcon = <OrganizationIcon organization={{consumer_icon: JLINC_LOGO}}/>

  const jlincNameAndTagLine = <OrganizationNameAndTagLine
    organizationName="JLINC LABS"
    organizationTagLine="Connect all the Silos!"
  />

  const exampleButtons = <Fragment>
    <Button type="primary" value="Join" />
    <Button type="primary" value="SISA" />
  </Fragment>

  return <div>
    <StyleguideExample
      title="default"
      render={() =>
        <IconRow
          icon={jlincIcon}
          row={jlincNameAndTagLine}
          buttons={exampleButtons}
        />
      }
    />
    <StyleguideExample
      title="with a link"
      render={() =>
        <IconRow
          href="/"
          icon={jlincIcon}
          row={jlincNameAndTagLine}
          buttons={exampleButtons}
        />
      }
    />
  </div>
}
