import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import DropdownMenu from 'components/DropdownMenu'
import Button from 'components/Button'
import IconButton from 'components/IconButton'

const COLORS = ['Red', 'Orange', 'Yellow', 'Blue', 'Green']

export default ({ state, setState }) => {
  const options = COLORS.map(color => ({
    value: color,
    onSelect: () => { setState({ color }) },
  }))
  options.splice(2, 0, '--')
  return <div>
    <p style={{
      backgroundColor: state.color,
      padding: '10px',
    }}>selected color: {state.color}</p>
    <StyleguideExample
      title={`default`}
      render={() =>
        <div>
          <DropdownMenu {...{options}}>
            <IconButton type="menu" />
          </DropdownMenu>
        </div>
      }
    />

    <StyleguideExample
      title={`rightAligned`}
      render={() =>
        <div>
          <DropdownMenu {...{options}} rightAligned>
            <IconButton type="menu" />
          </DropdownMenu>
        </div>
      }
    />

    <StyleguideExample
      title={`button`}
      render={() =>
        <div>
          <DropdownMenu {...{options}}>
            <Button type="primary">Menu</Button>
          </DropdownMenu>
        </div>
      }
    />

    <StyleguideExample
      title={`looking like a ContentBox`}
      render={() =>
        <div>
          <DropdownMenu {...{options}}>
            <IconButton type="menu" bordered/>
          </DropdownMenu>
        </div>
      }
    />
  </div>
}
