import { h, Fragment } from 'preact'
import { useState } from 'preact/hooks'

import StyleguideExample from 'components/StyleguideExample'
import Switch from 'components/Switch'

export default () => {
  const [checked, setChecked] = useState(false)
  const [saving, setSaving] = useState(false)
  const simulateSave = checked => {
    setSaving(true)
    setTimeout(
      () => {
        setChecked(checked)
        setSaving(false)
      },
      500,
    )
  }
  return <Fragment>
    <StyleguideExample
      title="checked"
      render={() =>
        <Switch {...{checked, onChange: setChecked }}/>
      }
    />

    <StyleguideExample
      title="checked (inverted)"
      render={() =>
        <Switch inverted {...{checked, onChange: setChecked }}/>
      }
    />

    <StyleguideExample
      title="simulates saving"
      render={() =>
        <Switch {...{checked, saving, onChange: simulateSave }}/>
      }
    />

    <StyleguideExample
      title="not checked"
      render={() =>
        <Switch />
      }
    />

    <StyleguideExample
      title="checked (disabled)"
      render={() =>
        <Switch checked disabled/>
      }
    />

    <StyleguideExample
      title="not checked (disabled)"
      render={() =>
        <Switch disabled/>
      }
    />

    <StyleguideExample
      title="not checked (saving)"
      render={() =>
        <Switch saving/>
      }
    />

    <StyleguideExample
      title="checked (saving)"
      render={() =>
        <Switch checked saving/>
      }
    />
  </Fragment>
}
