import { h, Fragment } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import Button from 'components/Button'
import OrganizationList from 'components/OrganizationList'

const allOrganizations = [
  {
    apikey: 'org1',
    name: 'Google',
    consumer_icon: '',
    description: 'this is org number 1',
  },
  {
    apikey: 'org2',
    name: 'Amazon',
    consumer_icon: '',
    description: 'this is org number 2',
  },
]
export default () =>
  <div>
    <StyleguideExample
      title="just organizations"
      render={() =>
        <OrganizationList
          organizations={allOrganizations}
        />
      }
    />
    <StyleguideExample
      title="organizations with some buttons"
      render={() =>
        <OrganizationList
          organizations={
            allOrganizations.map(organization => {
              return {
                ...organization,
                iAdmin: Math.random() > 0.5,
                buttons: <Fragment>
                  <Button
                    type="normal"
                    value="remove"
                  />
                  &nbsp;
                  <Button
                    type="primary"
                    value="Connect"
                  />
                </Fragment>,
              }
            })
          }
        />
      }
    />

  </div>
