import { h } from 'preact'

import StepDots from 'components/StepDots'

export default ({ location: { query: { d: selectedDotIndex } } }) => {
  const dots = Array(10).fill().map((_, index) => {
    return {
      href: `/components/StepDots?d=${index}`,
      selected: selectedDotIndex === `${index}`,
    }
  })
  return <div>
    <StepDots dots={dots} />
  </div>
}
