import { h } from 'preact'
import { useRef, useState, useCallback, useEffect } from 'preact/hooks'

import usePrevious from 'lib/usePreviousHook'

import Spotlight from 'components/Spotlight'

export default () => {
  const spotlightRef = useRef()
  const [target, setTarget] = useState()
  const [circular, setCircular] = useState(true)
  const previousTarget = usePrevious(target)

  useEffect(
    () => {
      const reposition = ({ animate = false }) => {
        const spotlight = spotlightRef.current.base
        Spotlight.pointAt({ spotlight, target, animate, circular })
      }
      reposition({ animate: !!previousTarget })
      window.addEventListener('resize', reposition)
      window.addEventListener('scroll', reposition)
      return () => {
        window.removeEventListener('resize', reposition)
        window.removeEventListener('scroll', reposition)
      }
    },
    [target, circular]
  )

  useEffect(
    () => {
      const buttons = spotlightRef.current.base.parentElement.querySelectorAll('button')
      const button = buttons[Math.floor(Math.random() * (buttons.length - 1))]
      setTarget(button)
    },
    [],
  )

  const pointAtMe = useCallback(event => {
    let target = event.target
    while(target && target.tagName !== 'BUTTON')
      target = target.parentElement
    setCircular(Math.random() > 0.5)
    setTarget(target)
    event.preventDefault()
    return false
  })

  const button = value =>
    <button onClick={pointAtMe}>{value}</button>

  const bigP = <p>
    Lorem ipsum enim ut id reprehenderit culpa in velit tempor
    adipisicing consectetur velit ea dolore reprehenderit dolore
    dolore ea eu aute ex do fugiat occaecat reprehenderit commodo
    sit duis ullamco enim et laboris.
  </p>
  return <div>
    <p>
      cirlce?&nbsp;
      <input
        type="checkbox"
        checked={!!circular}
        onChange={event => { setCircular(event.target.checked) }}
      />
    </p>
    <Spotlight ref={spotlightRef} />
    <p>click anywhere on the page to move the spotlight</p>
    {button('hello')}
    {button('this is a longer button')}
    {button(
      <div>
        <p>even bigger</p>
        <p>button</p>
      </div>
    )}
    {button('some')}
    {button('more')}
    {button(<div>{bigP}</div>)}
    {button('small buttons')}
    {button(
      <div>
        <p>WHAM!</p>
        <p>BAM!</p>
        <p>SLAM!</p>
      </div>
    )}
    {button('some')}
    {button('more')}
    {button('small buttons')}
    {button(
      <div>
        {Array(3).fill().map(() => bigP)}
      </div>
    )}
  </div>
}
