import { h } from 'preact'
import { useState } from 'preact/hooks'

import StyleguideExample from 'components/StyleguideExample'
import PhoneInput from 'components/PhoneInput'

export default () => {
  const [value, setValue] = useState('')
  return <div>
    <div><pre>"{value}"</pre></div>
    <StyleguideExample
      title="onInput"
      render={() =>
        <PhoneInput {...{value, onInput: setValue}}/>
      }
    />
    <StyleguideExample
      title="onChange"
      render={() =>
        <PhoneInput {...{value, onChange: setValue}}/>
      }
    />
    <StyleguideExample
      title="callingCode={44}"
      render={() =>
        <PhoneInput {...{value, onInput: setValue, callingCode: 44}}/>
      }
    />
    <StyleguideExample
      title="disabled"
      render={() =>
        <PhoneInput disabled {...{value}}/>
      }
    />
  </div>
}
