import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import ButtonGroupInput from 'components/ButtonGroupInput'
import Icon from 'components/Icon'

export default ({ state, setState }) =>
  <div>
    <StyleguideExample
      title="options as strings"
      render={() =>
        <div>
          <div>size: {state.example1Value}</div>
          <ButtonGroupInput
            value={state.example1Value}
            onChange={example1Value => setState({ example1Value })}
            options={['small', 'medium', 'large']}
          />
        </div>
      }
    />

    <StyleguideExample
      title="disabled"
      render={() =>
        <div>
          <ButtonGroupInput
            disabled
            value={state.example1Value}
            onChange={example1Value => setState({ example1Value })}
            options={['small', 'medium', 'large']}
          />
        </div>
      }
    />

    <StyleguideExample
      title="options as nodes and partially disabled"
      render={() => {
        const icons = Icon.types.slice(1, 7)
        return <div>
          <div>favorite Icon: {state.example2Value}</div>
          <ButtonGroupInput
            value={state.example2Value}
            onChange={example2Value => setState({ example2Value })}
            options={
              icons.map(icon => {
                return {
                  value: icon,
                  buttonValue: <Icon type={icon} />,
                  disabled: icon === Icon.types[2],
                }
              })
            }
          />
        </div>
      }}
    />

    <StyleguideExample
      title="fat with options map"
      render={() => {
        return <div>
          <div>HUGE: {state.example3Value}</div>
          <ButtonGroupInput
            fat
            value={state.example3Value}
            onChange={example3Value => setState({ example3Value })}
            options={[
              {value: 'open to join', buttonValue: 'OPEN TO JOIN'},
              {value: 'request to join', buttonValue: 'REQUEST TO JOIN'},
              {value: 'invite only', buttonValue: 'INVITE ONLY'},
            ]}
          />
        </div>
      }}
    />

    <StyleguideExample
      title="block"
      render={() => {
        return <div>
          <div>private?: {state.example4Value ? 'true' : 'false'}</div>
          <ButtonGroupInput
            fat
            block
            value={!!state.example4Value}
            onChange={example4Value => setState({ example4Value })}
            options={[
              {value: false, buttonValue: 'PUBLIC'},
              {value: true, buttonValue: 'PRIVATE'},
            ]}
          />
        </div>
      }}
    />

    <StyleguideExample
      title="unsaved when closed"
      render={() => {
        return <div>
          <div>open: {state.example5Value}</div>
          <ButtonGroupInput
            unsaved={state.example5Value === 'closed'}
            value={state.example5Value || 'open'}
            onChange={example5Value => setState({ example5Value })}
            options={['open', 'closed']}
          />
        </div>
      }}
    />
  </div>
