import { h } from 'preact'

import COUNTRIES from 'lib/countries'

import Dropdown from 'components/Dropdown'
import InspectObject from 'components/InspectObject'
import StyleguideExample from 'components/StyleguideExample'

const COLORS = ['Red', 'Orange', 'Yellow', 'Blue', 'Green']


export default ({ state, setState }) =>
  <div>

    <StyleguideExample
      title={`options as array`}
      render={() =>
        <div>
          <Dropdown
            placeholder="choose a color"
            value={state.color}
            onChange={color => setState({ color })}
            options={COLORS}
          />
          <InspectObject object={state.color}/>
        </div>
      }
    />

    <StyleguideExample
      title={`disabled`}
      render={() =>
        <div>
          <Dropdown
            placeholder="choose a color"
            value={state.color}
            onChange={color => setState({ color })}
            options={COLORS}
            disabled
          />
          <InspectObject object={state.color}/>
        </div>
      }
    />

    <StyleguideExample
      title={`unsaved`}
      render={() =>
        <div>
          <Dropdown
            placeholder="choose a color"
            value={state.color}
            onChange={color => setState({ color })}
            options={COLORS}
            unsaved
          />
          <InspectObject object={state.color}/>
        </div>
      }
    />

    <StyleguideExample
      title={`noIcon`}
      render={() =>
        <div>
          <Dropdown
            placeholder="choose a color"
            value={state.color}
            onChange={color => setState({ color })}
            options={COLORS}
            noIcon
          />
          <InspectObject object={state.color}/>
        </div>
      }
    />

    <StyleguideExample
      title={`options as option`}
      render={() =>
        <div>
          <Dropdown
            value={state.country}
            onChange={country => setState({ country })}
            options={invertObject(COUNTRIES)}
          />
          <InspectObject object={state.country}/>
        </div>
      }
    />

    <StyleguideExample
      title={`noOptions`}
      render={() =>
        <div>
          <Dropdown
            value=""
            onChange={() => {}}
            options={[]}
            noOptions={'nothing to  here'}
          />
          <InspectObject object={state.country}/>
        </div>
      }
    />

    <StyleguideExample
      title={`postMessage`}
      render={() =>
        <div>
          <Dropdown
            value={state.color}
            onChange={color => setState({ color })}
            options={COLORS}
            postMessage={'contact site owner to add more colors'}
          />
          <InspectObject object={state.country}/>
        </div>
      }
    />

  </div>

function invertObject(object){
  return Object.fromEntries(
    Object.entries(object).map(([key, value]) => [value, key])
  )
}
