import { h, Component } from 'preact'
import PropTypes from 'prop-types'

import Link from 'components/Link'
import Header from 'components/Header'
import './index.sass'

export default class StyleguideExample extends Component {

  propTypes = {
    title: PropTypes.string.isRequired,
    render: PropTypes.func.isRequired,
  }

  state = { open: false }

  toggle = () => {
    this.setState({ open: !this.state.open })
  }

  render(){
    const { title, render } = this.props
    const { open } = this.state
    const source = render.toString()
      // `Object(preact__WEBPACK_IMPORTED_MODULE_0__["h"])`
      .replace(/Object\(p?react[^\)]+\)/g, 'h')

      // `preact__WEBPACK_IMPORTED_MODULE_0__["Fragment"]`
      .replace(/p?react__[^\]]+\["([^\"]+)"\]/g, '$1')

      // .`replace(/react__WEBPACK_IMPORTED_MODULE_\d+__\["default"\]/g, 'h')`
      .replace(/components_(\w+)__WEBPACK_IMPORTED_MODULE_\d+__\["default"\]/g, '$1')

      // // `_JLINC_LOGO_EXAMPLE_txt__WEBPACK_IMPORTED_MODULE_1___default`
      .replace(/_(\w+)__WEBPACK_IMPORTED_MODULE_\d+___default.a/g, '$1')
    return <div className="StyleguideExample">
      <Header size="md">Example: {title}</Header>
      <div className="StyleguideExample-content">{render()}</div>
      <Link onClick={this.toggle} tabIndex={-1}>{open ? 'hide source' : 'show source'}</Link>
      { open && <pre className="StyleguideExample-code"><code>{source}</code></pre> }
    </div>
  }
}
