import { h, Fragment } from 'preact'

import { useLocationQueryState } from 'resources/location'

import StyleguideExample from 'components/StyleguideExample'
import CopyTextInput from 'components/CopyTextInput'
import Dropdown from 'components/Dropdown'
import Icon from 'components/Icon'
import IconButton from 'components/IconButton'

const ICONS = [
  ...[...Icon.types].sort().map(type => ({ type })),
  { value: '' },
  { value: '' },
  { value: '' },
  { value: '' },
]

export default ({state, setState}) => {

  const [type, setType] = useLocationQueryState(
    {query: 't', defaultValue: Icon.types[1] },
    'IconButton/examples',
  )
  const [size, setSize] = useLocationQueryState(
    {query: 's', defaultValue: Icon.sizes[0] },
    'IconButton/examples',
  )

  return <div>
    <Dropdown value={type} onChange={setType} options={Icon.types} />
    <Dropdown value={size} onChange={setSize} options={Icon.sizes} />

    {Icon.sizes.map(size =>
      <p>
        <IconButton type={type} size={size} />
        &nbsp;
        <CopyTextInput value={`<IconButton type="${type}" size="${size}" />`} />
      </p>
    )}


    <StyleguideExample
      title="just icons"
      render={() =>
        <Fragment>
          {ICONS.map(props => <IconButton {...props} />)}
        </Fragment>
      }
    />

    <StyleguideExample
      title="custom children"
      render={() =>
        <IconButton
          onClick={() => {
            setState({ count: (state.count || 0) + 1 })
          }}
        >
          {state.count || 0}
        </IconButton>
      }
    />

    <StyleguideExample
      title="bordered"
      render={() =>
        <Fragment>
          {ICONS.map(props => <IconButton {...props} bordered />)}
        </Fragment>
      }
    />

    <StyleguideExample
      title="bordered and padded"
      render={() =>
        <Fragment>
          {ICONS.map(props => <IconButton {...props} bordered padded />)}
        </Fragment>
      }
    />

    <StyleguideExample
      title="disabled"
      render={() =>
        <Fragment>
          {ICONS.map(props => <IconButton {...props} disabled />)}
        </Fragment>
      }
    />
  </div>
}
