import { h } from 'preact'

import Form from 'components/Form'
import FormBuilder from 'components/FormBuilder'
import CountryDropdownPicker from 'components/CountryDropdownPicker'
import PhoneAndCountryInput from 'components/PhoneAndCountryInput'
import PriceInput from 'components/PriceInput'
import InspectObject from 'components/InspectObject'
import StyleguideExample from 'components/StyleguideExample'

export default ({ state, setState }) => {
  const onChange = profileChanges => {
    for (const key in profileChanges)
      if (profileChanges[key] === '') delete profileChanges[key]
    setState({ profileChanges })
  }
  const onReset = () => { setState({ profileChanges: undefined }) }
  const onSubmit = profile => {
    setState({
      updatingProfile: true,
    })
    setTimeout(() => {
      setState({
        updatingProfile: undefined,
        profile: {...state.profile, ...profile},
        profileChanges: undefined,
      })
    }, 1000)
  }
  const validator = (value, persistedValue) => {
    const combined = {...persistedValue, ...value}
    if (!combined.firstName || combined.firstName.length < 3)
      return 'First name must be at least 3 characters'
  }

  const renderForm = showLabel =>
    form =>
      <div>
        <Form.Row>
          {form.textItem({
            valueProp: 'firstName',
            label: showLabel && 'FIRST NAME',
            placeholder: 'first name',
            required: true,
          })}
          {form.textItem({
            valueProp: 'lastName',
            label: showLabel && 'LAST NAME',
            placeholder: 'last name',
          })}
        </Form.Row>
        <Form.Row>
          {form.textItem({
            valueProp: 'username',
            label: showLabel && 'USERNAME',
            placeholder: 'username',
            required: true,
          })}
          {form.textItem({
            valueProp: 'password',
            label: showLabel && 'PASSWORD',
            placeholder: 'password',
            password: true,
            required: true,
          })}
        </Form.Row>
        <Form.Row>
          {form.item({
            valueProp: 'country',
            label: showLabel && 'COUNTRY',
            placeholder: 'country',
            input: <CountryDropdownPicker readOnly={false}/>
          })}
          {form.item({
            valueProp: 'mobile',
            bindTo: 'onInput',
            label: showLabel && 'MOBILE PHONE',
            placeholder: '+1 555 555 5555',
            input: <PhoneAndCountryInput
              name="tel"
              autocomplete="tel"
              required
              lpignore={false}
            />,
          })}
        </Form.Row>
        <Form.Row>
          {form.item({
            valueProp: 'price_low',
            label: 'MINIMUM PRICE',
            input: <PriceInput
              onCurrencySelect={currency => { form.change({currency}) }}
              currency={form.value.currency}
            />
          })}
          {form.item({
            valueProp: 'price_high',
            label: 'MAXIMUM PRICE',
            input: <PriceInput
              onCurrencySelect={currency => { form.change({currency}) }}
              currency={form.value.currency}
            />
          })}
        </Form.Row>
        <Form.Row>
          {form.textItem({
            valueProp: 'ssn',
            label: showLabel && 'SOCIAL SECURUTY NUMBER',
            placeholder: 'Social Securuty Number',
            disabled: true,
          })}
        </Form.Row>
        {form.textAreaItem({
          valueProp: 'body',
          label: showLabel && 'LIKES, DISLIKES, FAVORITE COLOR WHATEVER',
          placeholder: 'I like',
        })}
        <Form.ButtonRow>
          {form.resetButton({})}
          {form.submitButton({
            value: 'Save profile',
            submittingValue: 'Saving profile',
          })}
        </Form.ButtonRow>
      </div>

  return <div>
    <StyleguideExample
      title="using placeholders"
      render={() =>
        <div>
          <FormBuilder
            error={null}
            defaultValue={{
              firstName: 'DEFAULT firstName',
              lastName: 'DEFAULT lastName',
              body: 'DEFAULT body',
            }}
            value={state.profileChanges || {}}
            persistedValues={state.profile || {}}
            submitting={!!state.updatingProfile}
            onChange={onChange}
            onSubmit={onSubmit}
            onReset={onReset}
            validator={validator}
            render={renderForm(false)}
          />
        </div>
      }
    />

    <StyleguideExample
      title="using labels"
      render={() =>
        <div>
          <FormBuilder
            error={null}
            value={state.profileChanges || {}}
            persistedValues={state.profile || {}}
            submitting={!!state.updatingProfile}
            onChange={onChange}
            onSubmit={onSubmit}
            onReset={onReset}
            validator={validator}
            render={renderForm(true)}
          />
        </div>
      }
    />
    <InspectObject object={state} />
  </div>
}
