import { h } from 'preact'
import Page from 'lib/Page'
import Header from 'components/Header'
import Link from 'components/Link'
import components from '../components'

export default class HomePage extends Page {

  static pageTitle = 'Styleguide'

  render() {
    return <div>
      <Header size="lg">JLINC Styleguide</Header>
      <p>Welcome to the JLINC Labs Styleguide.</p>
      <p>
        The purpose of this styleguide is to display our suite of web components.
        Choose a component in the sidebar to see examples of the components we have.
      </p>
      <Header size="md">Components</Header>
      <ul>
        {
          Object.keys(components).sort().map(component =>
            <li>
              <Link
                href={`/components/${component}`}
              >{component}</Link>
            </li>
          )
        }
      </ul>

    </div>
  }
}
