import { h } from 'preact'

import { useLocalStorage } from 'lib/storageHooks'

import StyleguideExample from 'components/StyleguideExample'
import TextArea from 'components/TextArea'

export default () => {
  const [value, onInput] = useLocalStorage('TextArea.examples', '')
  return <div>
    {
      [
        {},
        {disabled: true},
        {immutable: true},
        {maxLength: 20, showCounter: true},
        {maxLines: 3},
        {minRows: 1, maxRows: 1},
        {maxLength: 20, maxLines: 2},
        {autoResizeVertically: true},
        {minRows: 2, maxRows: 4},
        {autoResizeVertically: true, minRows: 2, maxRows: 4},
        {minRows: 2, maxRows: 3, maxLines: 4},
      ].map(props =>
        <StyleguideExample {...{
          title: JSON.stringify(props),
          render: () => <TextArea {...{value, onInput, ...props}}/>
        }}/>
      )
    }
  </div>
}
