import { h } from 'preact'

import RawHTML from 'components/RawHTML'
import ContentBox from 'components/ContentBox'
import Header from 'components/Header'

export default () =>
  <div>
    <Header size="md">Example 1</Header>
    <ContentBox padded>
      <RawHTML source={example1}/>
    </ContentBox>
    <hr/>

    <Header size="md">Example 1 (ignore: [button, p])</Header>
    <ContentBox padded>
      <RawHTML source={example1} ignoreNodes={['button', 'p']} />
    </ContentBox>
  </div>

