import PathnameRouter from 'lib/PathnameRouter'

import HomePage from './pages/HomePage'
import ComponentsPage from './pages/ComponentsPage'
import TextPage from './pages/TextPage'
import ColorsPage from './pages/ColorsPage'
import ThemesPage from './pages/ThemesPage'
import ErrorsPage from './pages/ErrorsPage'
import AppStateTestPage from './pages/AppStateTestPage'

export default new PathnameRouter(map => {
  map('/', HomePage)
  map('/text', TextPage)
  map('/colors', ColorsPage)
  map('/themes', ThemesPage)
  map('/errors', ErrorsPage)
  map('/app-state-test', AppStateTestPage)
  map('/components', ComponentsPage)
  map('/components/:component', ComponentsPage)
  map('/components/:component/:path*', ComponentsPage)
  map(/.*/, PageNotFound)
})

function PageNotFound(...args){
  console.log(`Page Not Found `, args)
  return `Page Not Found`
}
