import { h } from 'preact'
import { useState, useCallback } from 'preact/hooks'

import WYSIWYGInput from 'components/WYSIWYGInput'
import WYSIWYGContent from 'components/WYSIWYGContent'
import TextInput from 'components/TextInput'
import Button from 'components/Button'
import Code from 'components/Code'
import ButtonRow from 'components/ButtonRow'
import ContentBox from 'components/ContentBox'

export default ({ state, setState }) => {
  const [allowUploads, setAllowUploads] = useState(true)
  const onExample1Change = useCallback(
    example1 => { setState({ example1 }) },
    []
  )
  return <div>
    <ButtonRow>
      <Button
        type="normal"
        value={state.example1disabled ? 'enable' : 'disable'}
        onClick={() => { setState({ example1disabled: !state.example1disabled }) }}
      />
      <Button
        type="normal"
        disabled={!state.example1}
        value="clear"
        onClick={() => { onExample1Change(undefined) }}
      />
      <Button
        type="normal"
        value={(allowUploads ? 'disallow' : 'allow') +  'uploads'}
        onClick={() => { setAllowUploads(!allowUploads) }}
      />
      {examples.map((example, index) =>
        <Button
          type="normal"
          onClick={() => { onExample1Change(example()) }}
          value={`example ${index + 1}`}
        />
      )}
    </ButtonRow>
    <br/>

    <TextInput placeholder="this is here so you can test tabbing"/>
    <br/>
    <WYSIWYGInput
      disabled={state.example1disabled}
      value={state.example1}
      onInput={onExample1Change}
      onChange={onExample1Change}
    />
    <br/>
    <TextInput placeholder="this is here so you can test tabbing"/>
    <br/>

    <ContentBox padded>
      <WYSIWYGContent source={state.example1}/>
    </ContentBox>
    <br/>
    <Code source={state.example1}/>
  </div>
}


