import { h } from 'preact'

import StyleguideExample from 'components/StyleguideExample'
import Timestamp from 'components/Timestamp'

const time = new Date()

export default () =>
  <div>

    <StyleguideExample
      title="missing time prop"
      render={() =>
        <Timestamp />
      }
    />

    <StyleguideExample
      title="default format"
      render={() =>
        <Timestamp time={time} />
      }
    />

    <StyleguideExample
      title="format=full"
      render={() =>
        <Timestamp time={time} format="full" />
      }
    />

    <StyleguideExample
      title="format=date"
      render={() =>
        <Timestamp time={time} format="date" />
      }
    />

    <StyleguideExample
      title="format=time"
      render={() =>
        <Timestamp time={time} format="time" />
      }
    />

    <StyleguideExample
      title="custom format"
      render={() =>
        <Timestamp time={time} format="YYYY::MM::DD" />
      }
    />
  </div>
