import { h, Fragment } from 'preact'
import { useState, useEffect } from 'preact/hooks'

import {
  ICON_SIZE,
  AVATAR_SIZE,
  BANNER_SIZE,
} from 'lib/imageHelpers'

import Header from 'components/Header'
import CropImageInput from 'components/CropImageInput'
import InspectObject from 'components/InspectObject'

import exampleIcon from './examples/icon.png'
import exampleAvatar from './examples/avatar.png'
import exampleBanner from './examples/banner.png'

const SIZES = {
  ICON_SIZE,
  AVATAR_SIZE,
  BANNER_SIZE,
}

export default ({ state, setState }) => {
  const [displayBlock, setDisplayBlock] = useState(false)
  const [disabled, setDisabled] = useState(false)
  useEffect(
    () => {
      setState({
        ICON_SIZE: exampleIcon,
        AVATAR_SIZE: exampleAvatar,
        BANNER_SIZE: exampleBanner,
      })
    },
    []
  )
  return <div>
    <style>
      {displayBlock
        ? `
          .CropImageInput{ display: block !important; }
        `
        : ``
      }
    </style>
    <div>
      <button onClick={() => { setDisplayBlock(!displayBlock)}}>
        {displayBlock ? 'display inline' : 'display block'}
      </button>
      <button onClick={() => { setDisabled(!disabled)}}>
        {disabled ? 'enable' : 'disable'}
      </button>
    </div>
    {Object.entries(SIZES).map(([name, {height, width}]) =>
      <Fragment>
        <Header size="sm">{name}: {height}x{width}</Header>
        <CropImageInput {...{
          disabled,
          circle: name === 'AVATAR_SIZE',
          height,
          width,
          title: `example if a ${name} input`,
          value: state[name],
          onChange: src => { setState({[name]: src}) },
        }}/>
      </Fragment>
    )}

    <div style={{width: 400, border: '5px solid blue'}}>
      <Header size="md">constrained by parent</Header>
      <Header size="sm">size: {BANNER_SIZE.height}x{BANNER_SIZE.width}</Header>
      <CropImageInput {...{
        disabled,
        height: BANNER_SIZE.height,
        width: BANNER_SIZE.width,
        title: `constrained example`,
        value: state['BANNER_SIZE'],
        onChange: src => { setState({BANNER_SIZE: src}) },
      }}/>
    </div>

    <style>
      {`
        .CropImageInputExample-width100 .CropImageInput{
          width: 100%;
        }
      `}
    </style>

    <div className="CropImageInputExample-width100">
      <Header size="md">constrained by parent</Header>
      <Header size="sm">size: {BANNER_SIZE.height}x{BANNER_SIZE.width}</Header>
      <CropImageInput {...{
        disabled,
        height: BANNER_SIZE.height,
        width: BANNER_SIZE.width,
        title: `constrained example`,
        value: state['BANNER_SIZE'],
        backgroundFillStyle: 'white',
        onChange: src => { setState({BANNER_SIZE: src}) },
      }}/>
    </div>

    <hr/>

    <InspectObject object={state} />

    {Object.keys(SIZES).map((name) =>
      <div>
        <img style={{outline: '2px dotted salmon'}} src={state[name]} />
      </div>
    )}
  </div>
}
