import { h } from 'preact'
import PropTypes from 'prop-types'
import ReactMarkdown from 'react-markdown'

import Link from 'components/Link'

export default function Markdown({ className, source, renderers, linkTarget }){
  return <ReactMarkdown {...{
    className: `Markdown ${className}`,
    source,
    renderers,
    linkTarget,
    disallowedTypes: ['image', 'html'],
  }}/>
}

Markdown.defaultProps = {
  className: '',
  renderers: { link: props => <Link type="link" {...props} rel="noopener noreferrer nofollow" /> },
  linkTarget: href => href.startsWith(window.location.origin) ? undefined : '_blank',
}

Markdown.propTypes = {
  className: PropTypes.string,
  source: ReactMarkdown.propTypes.source.isRequired,
  renderers: ReactMarkdown.propTypes.renderers,
  linkTarget: ReactMarkdown.propTypes.linkTarget,
}
