import { h } from 'preact'

import InspectObject from 'components/InspectObject'
import BobsPersonalDataConfigurationForm from 'components/BobsPersonalDataConfigurationForm'

const merge = (left, right) => {
  const changes = {...left, ...right}
  for(const key in changes){
    if (typeof changes[key] === 'undefined')
      delete changes[key]
  }
  return changes
}

export default ({ state, setState }) =>
  <div>
    <BobsPersonalDataConfigurationForm
      requestedData={state.requested_data || {}}
      changes={state.changes || {}}
      disabled={!!state.saving}
      error={state.error}
      onChange={changes => {
        setState({ changes: merge(state.changes, changes) })
      }}
      onReset={() => {
        setState({ changes: {} })
      }}
      onSave={() => {
        setState({
          requested_data: merge(state.requested_data, state.changes),
          changes: {},
        })
      }}
    />
    <InspectObject object={state} />
  </div>
