import { h, Fragment } from 'preact'

import { useLocationQueryState } from 'resources/location'

import Icon from 'components/Icon'
import CopyTextInput from 'components/CopyTextInput'
import Dropdown from 'components/Dropdown'
import Checkbox from 'components/Checkbox'

const SIZES =  ['xs', 'sm', undefined, 'lg']
const TYPES = [...Icon.types].sort()

const COLORS = 'black red orange yellow blue green purple'.split(' ')

const WORDS = `
Labore est deserunt adipisicing elit sed cupidatat consectetur officia
irure incididunt nisi in eu veniam eiusmod aliquip cillum dolor amet
do ut commodo minim sint sint ut in culpa ut do mollit nisi laborum
ullamco incididunt pariatur sed occaecat eu do exercitation id minim
culpa proident ullamco officia consectetur in duis et aute laboris eu
commodo culpa sit officia dolor velit anim enim ut ad laborum incididunt
`.split(/\s+/)

const rand = (min, max) => Math.floor(Math.random() * (max - min + 1) + min)

const someWords = () => {
  const index = rand(0, WORDS.length - 6)
  return WORDS.slice(index, index + rand(1, 6)).join(' ')
}

export default () => {
  const [type, setType] = useLocationQueryState(
    {query: 't', defaultValue: Icon.types[1] },
    'Icon/examples'
  )
  let [size, setSize] = useLocationQueryState(
    {query: 's', defaultValue: SIZES[0] },
    'Icon/examples'
  )
  if (size === 'undefined') size = undefined
  const [color, setColor] = useLocationQueryState(
    {query: 'c', defaultValue: COLORS[0] },
    'Icon/examples'
  )
  let [blue, setBlue] = useLocationQueryState(
    {query: 'b', defaultValue: false },
    'Icon/examples'
  )
  blue = blue === 'true'

  return <div>
    <div className="flex-row">
      <Dropdown value={type} onChange={setType} options={TYPES} />
      &nbsp;
      <div>
        {SIZES.map(size => <Icon {...{key: type, type, size}}/>)}
      </div>
      &nbsp;
      <CopyTextInput
        className="flex-grow"
        value={`<Icon type="${type}" ${size ? `size="${size}" ` : ''} ${blue ? 'blue ' : ''}/>`}
      />
    </div>

    <br/>
    <Dropdown value={color} onChange={setColor} options={COLORS} />
    <Dropdown
      value={size || 'undefined'}
      onChange={setSize}
      options={SIZES.map(x => x || 'undefined')}
    />
    <label> blue? <Checkbox value={!!blue} onChange={setBlue} /></label>
    <SizeTest {...{ size, color, blue, setType }}/>
    <div style={{columns:'0px 4', color}}>
      {[undefined, 'badtype', ...TYPES].map(type =>
        <div
          key={type}
          title={`<Icon type="${type}" />`}
          style={{
            margin:'5px 0',
            cursor:'pointer',
            outline: '1px dotted green',
          }}
          onClick={() => { setType(type) }}
        >
          <Icon {...{type, size, blue}} />
          <span style={{outline: '1px dotted orange'}}> {type}</span>
        </div>
      )}
    </div>
    <br/>


    <div>
      {TYPES.map(type =>
        <Fragment>
          <span>{someWords()}</span>&nbsp;
          <Icon type={type} size={size} />&nbsp;
          <span>{someWords()}</span>
        </Fragment>
      )}
    </div>

    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      {[...Icon.sizes].sort().map(size =>
        <div style={{display: 'flex', flexFlow: 'row wrap'}}>
          {TYPES.map(type =>
            <Fragment>
              <Icon {...{type, size, blue}} />
              &nbsp;
            </Fragment>
          )}
        </div>
      )}
    </div>
  </div>
}


function SizeTest({ size, color, blue, setType }){
  const renderIcon = type =>
    <Icon
      type={type}
      blue={blue}
      size={size}
      onClick={() => { setType(type) }}
    />

  const types = [...TYPES]
  const rows = []
  while (types.length > 0){
    rows.push(
      <div className="Icon-examples-size-test-row">
        {types.splice(0, 10).map(renderIcon)}
      </div>
    )
  }

  return <Fragment>
    <style>
      {`
        .Icon-examples-size-test{
          border: 1px dotted rgba(0, 0, 255, 0.5);
          margin-bottom: 10px;
          color: ${color};
        }
        .Icon-examples-size-test-row{
          border: 1px dotted rgba(255, 0, 0, 0.5);
        }
        .Icon-examples-size-test > .Icon{
          // border: 1px dotted rgba(255, 0, 0, 0.5);
        }
      `}
    </style>
    <div className="Icon-examples-size-test">
      {rows}
    </div>
  </Fragment>
}
